.aside{
  display: none;
  position: fixed;
  z-index: 110;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-120%);
  opacity: 0;
  transition: .5s;
  background: #EDF3F5;
  overflow: auto;

  &.active{
    transform: translateX(0);
    opacity: 1;

    &+.aside__overlay{
      display: block;
    }
  }

  &+.aside__overlay{
    position: fixed;
    z-index: 109;
    display: none;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.3);
  }

  &__wrap{
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    padding: 2vh 4.2vw 4vh;
  }
  &__sup{
    display: flex;
    align-items: center;
    margin-bottom: 10vw;
  }
  &__close{
    position: relative;
    margin-left: auto;
    color: #000;
  }
  &__search{
    width: 28500/360vw;

    .search__wrap{
      background: #fff;
    }
  }
  &__profile{
    width: 16400/360vw;
    margin-right: 1500/360vw;
  }
  &__head{
    margin-bottom: 5vh;
    display: flex;
    flex-wrap: wrap;
  }
  &__btn-1{
    margin-right: 800/360vw;
  }
  &__cart{
    width: 15600/360vw;
    margin-top: 1400/360vw;
  }
  &__cat{
    margin-right: auto;
  }
  &__menu{
    margin-bottom: auto;
  }
  &__footer{
    margin-top: 4vh;
    display: flex;
    align-items: center;
  }
  &__phone{
    margin-right: 1800/360vw;
  }
  &__soc-1{
    margin-right: 1000/360vw;
  }
  .social-link{
    width: 4000/360vw;
  }
  .box-phones__label{
    background: #fff;
		font-size: 1600/360vw;
	}
}



@media (min-width: @w-lg) {
	.aside{}
}
