.contacts{
  padding-bottom: 4vw;

  &__col-2{
    margin-top: 12vw;
  }
  &__row-1{
    margin-top: 8vw;
  }
  &__col-1-1{
    margin-bottom: 4vw;

    .box-phones__label{
      padding: 0;
      background: none;
      font-size: 1800/360vw;
      font-weight: 700;

      &:hover::after{
        background-color: var(--color-2);background-image: url(../img/svg/dropdown-1-1.svg);
      }

      &::after{
        display: block;
        align-self: center;
        width: 23/36em;
        flex: 0 0 23/36em;
        height: 23/36em;
        background-image: url(../img/svg/dropdown-1-1a.svg);
        background-color: var(--color-main);
        border-radius: 50%;
        margin-left: 0.4em;
        filter: drop-shadow(4px 4px 12px rgba(0, 0, 0, 0.20));
        transition: 0.3s background;
      }
    }
  }
  &__label{
    display: block;
    margin-top: 0.2em;
    font-size: 0.8em;
    color: #8B8B8B;
  }
  &__email{
    display: inline-block;
    font-size: 1800/360vw;
    font-weight: 700;
    white-space: nowrap;

    &::before{
      content: '';
      display: inline-block;
      vertical-align: middle;
      position: relative;
      top: -0.1em;
      margin-right: 0.5em;
      width: 46/36em;
      height: 46/36em;
      background: url(../img/svg/email-2.svg) center / contain no-repeat;
    }
  }

  &__addresses{
    margin-top: 10vw;
  }
  &__address{
    font-size: 1800/360vw;
    font-weight: 700;
    display: flex;
    line-height: 1.1;

    &+&{
      margin-top: 0.35em;
    }

    &::before{
      content: '';
      display: block;
      position: relative;
      top: -0.2em;
      margin-right: 0.3em;
      width: 46/36em;
      flex: 0 0 46/36em;
      height: 46/36em;
      background: url(../img/svg/pin-2.svg) center / contain no-repeat;
    }
  }

  &__maps{
    margin-top: 12vw;
  }
  &__map{
    margin-top: 3vw;
    width: 100%;
    height: 60vh;
    position: relative;
    overflow: hidden;
    border-radius: 6vw;
    background: #F6F6F7;
  }

  &__aside{
    position: relative;
    overflow: hidden;
    border-radius: 6vw;

    &-desc{
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      padding: 8vw;
    }
    &-title{
      display: block;
      margin-bottom: 6vw;
      font-weight: 700;
      font-size: 1.3em;
      line-height: 1.16;

      strong{
        color: var(--color-main);
      }
    }
    &-txt{
      margin-bottom: 8vw;
      font-size: 0.8em;
      margin-right: 10vw;
      line-height: 1.45;
    }
    
  }
}


@media (min-width: @w-lg) {
	.contacts{
    padding-bottom: 3.7vw;

    &__col-1{
      width: 99000/1920vw;
    }
    &__col-2{
      margin-top: 0;
      margin-left: auto;
      width: 63000/1920vw;
    }

    &__row-1{
      margin-top: 3.1vw;
      display: flex;
    }
    &__col-1-1{
      margin-right: 4.6vw;
      margin-bottom: 0;

      .box-phones__label{
        font-size: 3600/1920vw;

        &::after{
          width: 28/36em;
          flex-basis: 28/36em;
          height: 28/36em;
        }
        &>i{
          width: 46/36em;
          flex-basis: 46/36em;
          margin-right: 0.7em;
        }
      }
    }
    &__email,
    &__address{
      font-size: 3600/1920vw;
    }
    &__address{
      &::before{
        top: -0.1em;
      }
    }
    &__addresses{
      margin-top: 2.9vw;
    }
    &__label{
      font-size: 1.15vw;
    }

    &__maps{
      margin-top: 2.9vw;
    }
    &__map{
      margin-top: 1vw;
      height: 44800/1920vw;
      border-radius: 2.6vw;
    }

    &__aside{
      border-radius: 2.6vw;

      &-desc{
        padding: 6600/1920vw 6600/1920vw;
      }
      &-title{
        margin-bottom: 2.6vw;
        font-size: 2.6vw;
      }
      &-txt{
        margin-bottom: 4vw;
        margin-right: 3vw;
        font-size: 1.15vw;
      }
    }
  }
}

@media (max-width: @w-lg) {
  .contacts{

    &__aside{
      .btn1{
        min-width: 72vw;
      }
    }
  }
}
