.cart-2{
  margin-top: 6vw;
  background: #edf3f5;
  padding: 8vw 6vw;
  border-radius: 6vw;
  
  &__fieldset{
    margin-bottom: 6vw;
    width: 100%;

    .form__box-1:last-child{
      margin-bottom: 0;
    }
  }
  &__header-1{
    display: block;
    margin-bottom: 6vw;
    font-weight: 700;
    font-size: 1.2em;
    line-height: 1.1;
  }

  &__order{
    padding: 8vw 6vw;
    border-radius: 6vw;
    background: rgba(209, 240, 221, 0.67);

    &-item{
      margin-bottom: 8vw;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: baseline;

      &:last-child{
        margin-bottom: 0;
      }
    }
    &-desc{
      width: 100%;
      margin-bottom: 2vw;
    }
    &-title{
      display: block;
      margin-bottom: 2vw;
      font-weight: 600;
      font-size: 1em;
      line-height: 1.2;
    }
    &-art{
      display: block;
      font-size: 0.8em;
      color: #739089;
    }
    &-price{
      font-weight: 700;
      font-size: 1.2em;
      color: var(--color-main);
      line-height: 1;
    }
    &-footer{
      margin-top: 10vw;
      font-size: 1.2em;
      font-weight: 700;

      strong{
        color: var(--color-main);
        display: block;
        font-weight: 800;
      }
    }
  }

  &__footer{
    margin-top: 12vw;

    &-sub{
      margin-top: 4vw;
      text-align: center;
      color: rgb(118, 118, 118);
      font-size: 0.8em;
    }
  }
  &__scale{
    margin-top: 6vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2vw;

    &-item{
      position: relative;
      width: 15%;
      padding: 0 0 15%;
      border-radius: 50%;
      background: #BEE6CE;
      border: 0;
      font-family: var(--font-averta);
      font-size: 1em;
      color: #93c3a6;
      transition: 0.3s color;

      &.active{
        background: var(--color-main);
        color: var(--color-main);

        &::after{
          opacity: 1;
        }
      }

      &::after{
        content: '';
        display: block;
        position: absolute;
        z-index: 3;
        width: 37%;
        height: 37%;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background: url(../img/svg/checkbox-3.svg) center / contain no-repeat;
        opacity: 0;
        transition: 0.2s opacity;
      }
      
      span{
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        z-index: 2;
        width: 5600/72%;
        height: 5600/72%;
        border: 2px solid #fff;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
    }

      &-line{
        display: block;
        height: 8px;
        border-radius: 4px;
        flex: 1;
        background: rgba(26, 201, 84, 0.31);

        &.active{
          background: var(--color-main);
        }
      }
    }

  &__fieldset-2,
  &__fieldset-3{
    margin-bottom: 10vw;
    width: 100%;
  }
  &__fieldset-4{
    width: 100%;
  }
  &__address{
    &-item{
      margin-bottom: 4vw;
      padding: 4vw;
      border-radius: 4vw;
      background: #FDFDFF;
    }
    &-head{
      display: flex;
      align-items: baseline;
      margin-bottom: 4vw;
    }
    &-title{
      display: block;
      flex: 1;
      font-weight: 700;
      white-space: nowrap;
    }
    &-txt{
      line-height: 1.5;
    }
    &-link{
      display: inline-block;
      font-size: 0.7em;
      margin-left: 1em;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-underline-offset: 0.2em;
      font-weight: 600;
      transition: 0.3s color;

      &:hover{
        text-decoration: none;
        color: var(--color-main);
      }

      &.color_green:hover{
        color: #000;
      }
    }
    &-btn{
      color: #000;
    }
  }
}


@media (min-width: @w-lg) {
	.cart-2{
    margin-top: 2.9vw;
    padding: 2.8vw 4.9vw 3vw;
    border-radius: 3500/1920vw;
    
    &__fieldset{
      margin-bottom: 0;
      padding-top: 0.75vw;
      width: 55800/1920vw;
      margin-right: auto;

      .form__label{
        margin-bottom: 0.6em;
      }
    }
    &__header-1{
      margin-bottom: 2vw;
      font-size: 3200/1920vw;
    }

    &__order{
      width: 85800/1920vw;
      border-radius: 3300/1920vw;
      padding: 3.65vw 2.2vw 3.7vw 3.7vw;

      &-items{
        margin-top: 2.5vw;
        padding-right: 2vw;
        max-height: 20vw;
        overflow-y: auto;

        &::-webkit-scrollbar-track{
          background-color: transparent;
          outline: 1px solid var(--color-main);
          outline-offset: 1px;;
          border: 1px solid #d1f0dd;
          border-radius: 6px;
        }
        &::-webkit-scrollbar{
          width:12px;
        }
        &::-webkit-scrollbar-thumb{
          border-radius: 7px;
          background-color: var(--color-main);
          // border: 1px solid #CFF2DD;
        }
      
        /* for Firefox */
        scrollbar-color: var(--color-main) #CFF2DD;
        scrollbar-width: thin;
      }
      &-item{
        margin-bottom: 2.1vw;
      }
      
      &-desc{
        margin-bottom: 0;
        width: 53%;
        padding-right: 5%;
      }
      &-title{
        margin-bottom: 0.5vw;
        font-size: 1.35vw;
      }
      &-art{
        font-size: .94vw;
      }
      &-count{
        width: 24%;
        font-size: 1.09vw;
      }
      &-price{
        width: 23%;
        font-size: 1.35vw;
      }
      &-footer{
        margin-top: 4.9vw;
        font-size: 3200/1920vw;

        strong{
          display: inline-block;
        }
      }
    }

    &__footer{
      margin-top: 7600/1920vw;
      display: flex;
      align-items: center;

      &-sub{
        margin: 0 2.6vw;
        font-size: 1.09vw;
        text-align: left;
      }
    }
    &__scale{
      margin-top: 0;
      width: 63400/1920vw;
      gap: .52vw;

      &-item{
        width: 7200/1920vw;
        padding-bottom: 7200/1920vw;
        font-size: 1.45vw;

        &._prev:hover{
          color: #fff;

          &::after{
            opacity: 0;
          }
        }
      }
      &-line{
        height: .8vw;
        border-radius: .42vw;
      }
    }

    &__fieldset-2{
      margin-bottom: 2.2vw;
      display: flex;
      flex-wrap: wrap;
    }
    &__fieldset-3{
      width: 76000/1920vw;
      margin-bottom: 0;
      margin-right: auto;

      .cart-2__header-1{
        margin-bottom: 1.25vw;
      }
    }
    &__address{
      display: flex;
      flex-wrap: wrap;
      gap: 3200/1920vw;

      &-item{
        margin: 0;
        width: 57000/1920vw;
        border-radius: 3500/1920vw;
        padding: 1.5vw 1.7vw 1.3vw 1.7vw;
      }
      &-head{
        margin-bottom: 1.3vw;
      }
      &-title{
        font-size: 1.46vw;
      }
      &-link{
        font-size: .94vw;
      }
      &-txt{
        font-size: 1.25vw;
        min-height: 4*1.5em;
      }
      &-btns{
        width: 15vw;
        align-self: center;
      }
      &-btn.btn2{
        min-width: 1px;
        width: 100%;
        font-size: 1.25vw;
      }
    }

    &__fieldset-4{
      width: 64000/1920vw;
      margin-right: 2vw;

      // .form__box-textarea{
      //   margin-left: 2vw;
      // }
    }
  }
}
