.same{
	margin-top: 6vw;

	&__slider{
		.swiper-slide{
			overflow: visible;
			height: auto;
		}
	}

	&__scrollbar{
		margin-top: 6vw;
	}
}


@media (min-width: @w-lg) {
	.same{
		margin-top: 3.8vw;

		&__slider{
			margin-right: 1.56vw;

			.swiper-slide{
				padding-right: 1.56vw;
			}
		}
		&__control{
			margin-top: 1.5vw;
			display: flex;
			align-items: center;
		}
		&__scrollbar{
			width: 143800/1920vw;
			margin-right: auto;
			margin-top: 0;
		}
		&__nav{
			margin: 0 3.12vw 0 0;
			width: 12200/1920vw;
		}
	}
}
