.ui-selectmenu-button.ui-button{
  width: 100%;
  height: 16vw;
  display: inline-flex;
  align-items: center;
  color: #000;
  // padding: .2em .8em .3em .5em;
  padding: 0.2em 0.8em 0.3em .8em;
  border: 2px solid #F5FBFC;
  border-radius: 2vw;
  background: #F5FBFC;
  font-family: var(--font-averta);
  outline: none;
  // font-size: .8em;
  font-size: 1em;
  transition: 0.3s border-color;

  .lg-min({
    height: 7700/1920vw;
    border-radius: .73vw;
    font-size: 1.04vw;
    padding: .2em 1.3em .3em;
  });

  &:hover{
    border-color: var(--color-main);
  }

  &.ui-selectmenu-button-open{

    .ui-selectmenu-icon.ui-icon{
      transform: rotate(180deg);
    }

  }

  .form__box-select_target &{

    &::before{
      content: '';
      display: block;
      width: 36/20em;
      flex: 0 0 36/20em;
      height: 36/20em;
      margin-right: 0.2em;
      background: url(../img/svg/icon-select-1.svg) center / contain no-repeat;

      .lg-min({
        width: 43/20em;
        flex: 0 0 43/20em;
        height: 43/20em;
        margin-right: 0.9em;
      });
    }
  }
  .form__box-select_region &{

    &::before{
      content: '';
      display: block;
      width: 36/20em;
      flex: 0 0 36/20em;
      height: 36/20em;
      margin-right: 0.9em;
      background: url(../img/svg/icon-select-2.svg) center / contain no-repeat;
    }
  }
  .form__box-select_city &{

    &::before{
      content: '';
      display: block;
      width: 36/20em;
      flex: 0 0 36/20em;
      height: 36/20em;
      margin-right: 0.9em;
      background: url(../img/svg/icon-select-3.svg) center / contain no-repeat;
    }
  }
  .form__box-select_sort &{

    &::before{
      content: '';
      display: block;
      width: 28/22em;
      flex: 0 0 28/22em;
      height: 28/22em;
      margin-right: 0.7em;
      background: url(../img/svg/sort.svg) center / contain no-repeat;
    }
  }
  .form__box-select_years &{

    &::before{
      content: '';
      display: block;
      width: 28/22em;
      flex: 0 0 28/22em;
      height: 28/22em;
      margin-right: 0.7em;
      background: url(../img/svg/select-year.svg) center / contain no-repeat;
    }
  }
  .form__box-select_months &{

    &::before{
      content: '';
      display: block;
      width: 28/22em;
      flex: 0 0 28/22em;
      height: 28/22em;
      margin-right: 0.7em;
      background: url(../img/svg/select-month.svg) center / contain no-repeat;
    }
  }
  .form__box-select_parts &{

    &::before{
      content: '';
      display: block;
      width: 28/22em;
      flex: 0 0 28/22em;
      height: 28/22em;
      margin-right: 0.7em;
      background: url(../img/svg/select-part.svg) center / contain no-repeat;
    }
  }
  .form__select_v2 &{
    border: 1px solid #b4c0bd;
    background: transparent;
  }

  .ui-selectmenu-icon.ui-icon{
    display: block;
    width: .95em;
    height: 12/20em;
    background: url(../img/svg/dropdown-2.svg) center / contain no-repeat;
    order: 2;
    position: relative;
    top: 0.1em;
    transition: 0.2s transform;
  }
  .ui-selectmenu-text{
    display: block;
    flex: 1;
  }
}
.ui-selectmenu-menu{
  max-width: calc(100% - 8.88vw - 12vw);
  background: #F5FBFC;
  border-radius: 2vw;
  padding: .5em 0;
  margin-top: 10px;
  line-height: 1.1;
  font-size: 0.9em;

  .lg-min({
    border-radius: .73vw;
    font-size: .94vw;
  });


  &._theme-icon-2{
    background: #FDFDFF;
  }
  &._fz22{
    .lg-min({
      font-size: 1.15vw;
    });
  }

  .ui-menu{
    overflow: auto;
    max-height: 30vh;
    
    .lg-min({
      max-height: 20vh;
    });

    &::-webkit-scrollbar-track{
      border-radius: 3px;
      background-color: #CFF2DD;
    }
    &::-webkit-scrollbar{
      width: 6px;
      background-color: #CFF2DD;
    }
    &::-webkit-scrollbar-thumb{
      border-radius: 3px;
      background-color: var(--color-main);
    }
  
    /* for Firefox */
    scrollbar-color: var(--color-main) #CFF2DD;
    scrollbar-width: thin;
  }
}
.ui-menu .ui-menu-item-wrapper.ui-state-active, .ui-menu .ui-menu-item-wrapper.ui-state-focus{
  margin: 0;
  font-weight: 600;
  color: var(--color-main);
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-underline-offset: 0.2em;
}
.ui-menu .ui-menu-item{
  padding: .4em 1.3em;

  &[aria-disabled="true"]{
    display: none;
  }
}
.ui-menu .ui-menu-item-wrapper{
  display: inline-block;
  padding: 0;
  transition: 0.3s color;
  line-height: 1.1;
}

.ui-tabs{
  padding: 0;
}
.ui-tabs .ui-tabs-nav li.ui-tabs-active{
  padding: 0;
  margin: 0;
}


.ui-slider{
  height: 3vw;
  border-radius: 1.5vw;
  background: #d1f4d8;

  .lg-min({
    height: .52vw;
    border-radius: .26vw;
  });
}
.ui-slider-horizontal .ui-slider-range{
  background: var(--color-main);
}
.ui-slider .ui-slider-handle{
  width: 8vw;
  height: 8vw;
  border-radius: 50%;
  top: -2.5vw;
  outline: none;
  border: 1.5vw solid #fff;
  background: var(--color-main);
  box-shadow: 0 0 1vw 0 rgba(75, 138, 104, 0.2);
  transition: 0.3s background;

  .lg-min({
    width: 3800/1920vw;
    height: 3800/1920vw;
    border-width: 700/1920vw;
    top: -.73vw;
  });

  &:hover{
    background: var(--color-2);
  }
}
.ui-front{
  z-index: 10000;
}

.ui-tabs .ui-tabs-panel,
.ui-tabs .ui-tabs-nav .ui-tabs-anchor{
  padding: 0;
}
