.counter__btn[disabled] {
  color: rgba(0, 0, 0, 0.2);
  cursor: default;
}
.counter__btn[data-type="minus"]::before,
.counter__btn[data-type="plus"]::before {
  content: '';
  display: block;
  position: absolute;
  height: 2px;
  width: 30%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: currentColor;
}
.counter__btn[data-type="plus"]::after {
  content: '';
  display: block;
  position: absolute;
  height: 2px;
  width: 30%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
  background: currentColor;
}
