.modal{
  position: relative;
  width: 96vw;
  margin: 0 auto;
  padding: 0;
  background: #FDFDFF;
  border-radius: 4vw;
  color: #000;

  &__wrap{
    max-height: calc(100vh - 40px);
    overflow: auto;
    padding: 10vw 6vw 10vw;
  }
  
  &__close{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 5;
    background: var(--color-main);
    opacity: 1;
    width: 10vw;
    height:10vw;
    top: -2vw;
    right: 6vw;
    border-radius: 2vw;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.3s background, 0.3s color;

    &:hover{
      background: var(--color-2);
      
      svg{
        opacity: 1;
      }
    }

    svg{
      width: 90%;
      fill: #fff;
      opacity: 0.5;
      transition: 0.3s opacity;
    }
  }
  &__title{
    display: block;
    line-height: 1.1;
    font-weight: 700;
    font-size: 1.4em;

    strong{
      color: var(--color-main);
    }
  }
  &__box{
    margin-top: 6vw;
    display: flex;
    align-items: center;
    font-size: 0.8em;
    line-height: 22/16;

    &>i{
      display: block;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 4vw;
    }
  }
  &__form{
    margin-top: 10vw;
  }
  &__txt{
    margin-top: 10vw;
    line-height: 24/16;

    p{
      margin-bottom: 1em;
    }

    ul{

      li{
        margin-bottom: 1em;
        position: relative;
        padding-left: 1.4em;

        &::before{
          content: '';
          display: block;
          position: absolute;
          left: 0;
          top: 0.4em;
          width: 12/16em;
          height: 12/16em;
          border-radius: 50%;
          background: var(--color-main);
        }
      }
    }
  }
  &__phones{
    margin-top: 10vw;
    line-height: 1.5;

    li{
      margin-bottom: 4vw;
    }
  }
  &__btn-double{
    margin-top: 10vw;
    text-align: center;

    &>div:nth-child(1){
      margin-bottom: 8vw;
    }
  }
  &__subtitle-1{
    margin-top: 4vw;
    font-size: 0.8em;
    line-height: 20/14;
    color: rgb(139, 139, 139);
  }
  &__diploms{
    margin-top: 6vw;

    &-scrollbar{
      display: none;
    }
    &-pic{
      position: relative;
      z-index: 1;

      &::after{
        content: '';
        display: block;
        padding-bottom: 56100/397%;
      }
      img{
        .positionFull();
        object-fit: contain;
      }
    }
    &-desc{
      position: absolute;
      z-index: 2;
      bottom: 0;
      right: 0;
      width: 100%;
      background: rgb(237, 243, 245);
      padding: 4vw;
      border-radius: 2vw;
    }
    &-publish{
      display: block;
      color: rgb(139, 139, 139);
      font-size: 0.8em;
    }
    &-title{
      display: block;
      font-weight: 700;
      font-size: 1.2em;
    }
  }

  &__tabs{
    margin-top: 6vw;

    &-wrap{
      margin-top: 4vw;
    }
  }
  &__sub{
    margin-top: 6vw;
    text-align: center;
    font-size: 0.8em;

    .link-1{
      font-size: 1em;
    }
  }

  .form__submit{
    box-shadow: none;
  }
}


@media (min-width: @w-lg) {
  .modal{
    width: 78000/1920vw;
    border-radius: 2.08vw;

    &&_w592{
      width: 59200/1920vw;
    }
    &&_w623{
      width: 62300/1920vw;
    }
    &&_w660{
      width: 66000/1920vw;
    }
    &&_w890{
      width: 89000/1920vw;
    }
    &&_w1192{
      width: 119200/1920vw;
    }

    &__wrap{
      padding: 2.6vw 2.6vw 2.6vw 2.6vw;
    }

    &__close{
      width: 5600/1920vw;
      height: 5600/1920vw;
      border-radius: .52vw;
      top: .94vw;
      right: -.94vw;

      svg{
        width: 2600/46%;
      }
    }
    &__title{
      font-size: 3600/1920vw;
    }
    &__box{
      margin-top: 1.5vw;
      margin-right: 5vw;
      font-size: .83vw;

      &>i{
        width: 5700/1920vw;
        flex-basis: 5700/1920vw;
        margin-right: .94vw;
      }
    }
    &__form{
      margin-top: 2vw;

      .form__box-submit-1{
        margin-top: 4400/1920vw;
      }
      .form__label{
        font-size: .83vw;
      }

      &&_v2{
        max-height: 50vh;
        padding-right: 2vw;
        overflow: auto;

        &::-webkit-scrollbar-track{
          border-radius: 4px;
          background-color: #CFF2DD;
        }
        &::-webkit-scrollbar{
          width: 8px;
          background-color: #CFF2DD;
        }
        &::-webkit-scrollbar-thumb{
          border-radius: 4px;
          background-color: var(--color-main);
        }
      
        /* for Firefox */
        scrollbar-color: var(--color-main) #CFF2DD;
        scrollbar-width: thin;
      }
    }
    &__txt{
      margin-top: 2vw;
      font-size: .83vw;
      padding-right: 2vw;
      max-height: 50vh;
      overflow: auto;

      &::-webkit-scrollbar-track{
        border-radius: 3px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar{
        width: 6px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 3px;
        background-color: var(--color-main);
      }
    
      /* for Firefox */
      scrollbar-color: var(--color-main) #CFF2DD;
      scrollbar-width: thin;
    }
    &__phones{
      margin-top: 2vw;
      font-size: 0.83vw;
      display: flex;
      flex-wrap: wrap;
  
      li{
        margin: 0 1.56vw 1.56vw 0;
      }
    }
    &__btn-double{
      margin-top: 2vw;
      margin-bottom: .52vw;
      display: flex;
  
      &>div:nth-child(1){
        margin-bottom: 0;
        margin-right: 1vw;
      }
    }
    &__subtitle-1{
      margin-top: 1.25vw;
      font-size: .73vw;
    }
    &__box-input-1{
      max-width: 56000/1920vw;
    }

    &__diploms{
      margin-top: 2vw;
      position: relative;
      padding: 0 4.7vw;

      .swiper-slide{
        padding: 0 1.5vw 1.56vw;
      }

      &-nav{
        margin: 0;
        position: static;

        .slider__arrow{
          position: absolute;
          z-index: 10;
          top: 50%;
          transform: translate(0,-50%);
        }
        .slider__arrow_prev{
          left: 0;
        }
        .slider__arrow_next{
          right: 0;
        }
      }
      &-desc{
        left: auto;
        width: 34600/1920vw;
        border-radius: 1.35vw;
        padding: 1.3vw;
      }
      &-title{
        font-size: 1.25vw;
      }
      &-publish{
        font-size: 0.83vw;
      }
    }

    &__tabs{
      margin-top: 2vw;

      &-wrap{
        margin-top: 3800/1920vw;
      }
    }
    &__sub{
      margin-top: 2.8vw;
      font-size: .73vw;
    }
  }
}