.catalog{
  margin-top: 6vw;
  padding-bottom: 6vw;

  &&_v2{
    margin-top: 10vw;
  }

  &__item{
    margin-bottom: 6vw;
  }
  &__card{
    padding: 8vw;
    border-radius: 8vw;
    background: #EDF3F5;
    display: flex;
    flex-direction: column;
  }
  &__pic{
    position: relative;
    display: block;
    overflow: hidden;
    border-radius: 4vw;
    margin-bottom: 4vw;

    &::after{
      content: '';
      display: block;
      padding-bottom: 25700/324%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    font-weight: 700;
    line-height: 1.25;
    font-size: 1.3em;
    // min-height: 2*1.25em;

    br{
      display: none;
    }
  }
  &__list{
    li{
      margin-bottom: 3vw;

      a{
        display: inline-block;

        &:hover{
          color: var(--color-main);
          font-weight: 700;
          text-decoration: underline;
          text-decoration-skip-ink: none;text-decoration-thickness: 1px;
          text-underline-offset: .2em;
        }
      }
    }
  }
  &__body{
    margin-bottom: 4vw;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
  }
  &__collapse{
		display: block;
		width: 5000/360vw;
		background: rgba(26, 201, 84, 0.20);
		border-radius: 800/360vw;
		color: var(--color-main);
		transition: 0.3s color, 0.3s background;
    border: 0;
    padding: 0;
    cursor: pointer;
    
    &:hover{
			background: var(--color-main);
			color: #fff;
		}

    &.active svg{
      transform: rotate(90deg);
    }

		svg{
			stroke: currentColor;
      transform: rotate(-90deg);
		}
	}
  &__link-diag{
    display: block;
    margin-left: auto;
  }

  &__goods{
    margin-top: 10vw;
  }

  &__sorts{
    margin-bottom: 6vw;
  }
  &__cards{

    &-item{
      margin-bottom: 6vw;
    }
  }

  &__footer{
    margin-top: 10vw;
  }
  &__count-pages{
    display: block;
    margin-bottom: 3vw;
    text-align: center;
    color: #9ca5ac;
    font-size: 0.8em;
  }

  &__support{
    position: relative;
    margin-top: 16vw;
    background: url(../img/catalog/bg-2-1.jpg) center / cover no-repeat;
    padding: 10vw 6vw 6vw;
    border-radius: 6vw;

    &-icon{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      right: 6vw;
      transform: translate(0%,-40%);
      width: 16vw;
      height: 16vw;
      border-radius: 50%;
      background: #fff;
      box-shadow: .73vw .73vw 2vw 0px rgba(56, 106, 94, 0.16);

      img{
        width: 4500/80%;
      }
    }
    &-head{
      display: flex;
      align-items: center;
      margin-bottom: 6vw;

      &>i{
        display: block;
        width: 30vw;
        flex: 0 0 30vw;
        margin-right: 6vw;
      }
      strong{
        display: block;
        margin-bottom: 0.6em;
        font-size: 1.2em;
        line-height: 1.2;
      }
      small{
        display: block;
        font-size: .8em;
        color: rgb(156, 165, 172);
        line-height: 1.43;
      }
    }
    &-txt{
      margin-bottom: 6vw;
      font-size: 0.9em;
      line-height: 1.62;
    }
    &-footer{
      text-align: center;
    }
    &-phone{
      display: inline-block;
      font-size: 1.4em;
      font-weight: 700;

      strong{
        color: var(--color-main);
      }
    }
    &-socials{
      margin-top: 4vw;

      .socials{
        justify-content: center;
      }
    }
  }
}


@media (min-width: @w-lg) {
	.catalog{
    margin-top: 3.7vw;
    padding-bottom: 8.8vw;

    &&_v2{
      margin-top: 1.7vw;
      padding-bottom: 3.1vw;
    }

    &__items{
      display: grid;
      gap: 3600/1920vw;
      grid-template-columns: repeat(4, 1fr);
    }
    &__item{
      margin: 0;
    }
    &__card{
      min-height: 37.5vw;
      border-radius: 2.08vw;
      padding: 3600/1920vw 3600/1920vw 3600/1920vw ;
    }
    &__pic{
      margin-bottom: 1.4vw;
      border-radius: 1.56vw;
    }
    &__title{
      margin-bottom: 1.1vw;
      height: 2*1.25em;
      font-size: 3200/1920vw;

      br{
        display: initial;
      }
    }
    &__list{
      margin-right: -1em;

      li{
        margin-bottom: .7vw;
        font-size: 1.04vw;
      }
    }
    &__body{
      margin-bottom: 1vw;
    }
    &__collapse{
      width: 5000/1920vw;
      border-radius: 800/1920vw;
    }


    &__sorts{
      margin-bottom: 4vw;
    }
    &__row-1{
      display: flex;
    }
    &__filters{
      width: 39600/1920vw;
    }
    &__goods{
      margin-top: 0;
      width: 120600/1920vw;
      margin-left: auto;
    }
    &__cards{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 2.08vw 1.56vw;

      &-item{
        margin: 0;
      }
    }
    &__footer{
      margin-top: 7000/1920vw;
      display: flex;
      justify-content: space-between;
      align-items: center;

      &::after{
        content: '';
        display: block;
        width: 25000/1920vw;
      }
    }
    &__count-pages{
      margin-bottom: 0;
      width: 25000/1920vw;
      font-size: 1.15vw;
      text-align: left;
    }

    
    &__support{
      margin-top: 5.1vw;
      padding: 1.5vw 1.7vw 1.8vw;
      border-radius: 2.6vw;

      &-icon{
        width: 8300/1920vw;
        height: 8300/1920vw;
        right: 1.8vw;
      }
      &-head{
        margin-bottom: 1.7vw;

        &>i{
          width: 13600/1920vw;
          flex-basis: 13600/1920vw;
          margin-right: 1.35vw;
        }
        strong{
          margin-top: 1.8vw;
          font-size: 1.45vw;
        }
        small{
          font-size: 0.73vw;
        }
      }
      &-txt{
        margin-bottom: 1.8vw;
        font-size: 0.83vw;
      }
      &-phone{
        font-size: 3200/1920vw;
      }
      &-socials{
        margin-top: 1vw;

        .socials{
          gap: 0.73vw;

          li{
            width: 6500/1920vw;
            border-radius: .83vw;
          }
        }
        .socials__link{
          padding: .7vw;
        }
      }
    }
  }
}
