.parts{
  margin-top: 6vw;
  padding-bottom: 12vw;

  &__aside{
    padding: 8vw 8vw;
    border-radius: 6vw;
    background: #EDF3F5;
  }
  &__mobile-select{
    .ui-selectmenu-button.ui-button{
      background: #fff;
      border: 0;
    }
  }
  &__list{
    display: none;

    li{
      display: flex;
      align-items: center;
      margin-bottom: 3vw;

      &:last-child{
        margin-bottom: 0;
      }
      &.active,
      &:hover{
        color: var(--color-main);
        font-weight: 700;

        i svg{
          fill: var(--color-main);
        }
      }

      &>i{
        display: block;
        width: .8em;
        flex: 0 0 .8em;
        height: .8em;
        margin-right: 0.5em;

        svg{
          fill: transparent;
          transition: 0.3s fill;
        }
      }
      &>a{
        display: block;

        &:hover{
          font-weight: 700;
          color: var(--color-main);
        }
      }
      
    }

  }

  &__content{
    margin-top: 8vw;
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    font-weight: 700;
    font-size: 1.3em;
    line-height: 1.1;
  }
  &__pic{
    display: block;
    margin-bottom: 4vw;
    overflow: hidden;
    border-radius: 6vw;
  }
  &__desc{
    h2{
      display: block;
      position: relative;
      margin-bottom: 4vw;
      font-size: 1.1em;
      font-weight: 600;
      line-height: 1.1;
    }
    p{
      margin-bottom: 0.8em;
    }
    ul{
      margin-left: 1.5em;

      li{
        margin-bottom: 0.8em;
        position: relative;
        
        &::before{
          content: '';
          display: block;
          position: absolute;
          left: -1.5em;
          top: 0.4em;
          width: 16/22em;
          height: 16/22em;
          background: url(../img/svg/li-item.svg) center / contain no-repeat;
        }
      }
    }
  }
  &__footer{
    margin-top: 6vw;

    a{
      display: inline-block;
      color: var(--color-main);
      font-weight: 700;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-underline-offset: .1em;
      transition: 0.3s color;

      &:hover{
        text-decoration: none;
        color: #000;
      }
    }
    strong{
      display: block;
      margin-bottom: 3vw;
      font-size: 1.1em;
      font-weight: 700;
      line-height: 1.2;
    }
  }
}


@media (min-width: @w-lg) {
	.parts{
    margin-top: 3.7vw;
    padding-bottom: 6.3vw;
    display: flex;
    align-items: flex-start;

    &__aside{
      width: 45000/1920vw;
      padding: 2.2vw 2.5vw 2.2vw;
      border-radius: 1.15vw;
      margin-right: 12000/1920vw;
    }
    &__list{
      display: block;
      font-size: 1.04vw;

      li{
        margin-bottom: .84vw;
      }
    }
    &__mobile-select{
      display: none;
    }

    &__content{
      flex: 1;
      margin-top: 0;
      padding-top: 0.2vw;
    }
    &__title{
      margin-bottom: 1.9vw;
      font-size: 3600/1920vw;
    }
    &__pic{
      margin-bottom: 1.6vw;
      border-radius: 5600/1920vw;
    }
    &__desc{
      position: relative;
      padding-left: 2.7vw;
      padding-top: .5vw;
      font-size: 1.04vw;
      line-height: 1.8;

      &::before{
        content: '';
        display: block;
        position: absolute;
        left: -0.3vw;
        top: 0.2vw;
        width: 4300/1920vw;
        height: 4300/1920vw;
        background: url(../img/partners/icon-2-1.svg) center / contain no-repeat;
      }

      h2{
        margin-bottom: 1.2vw;
        font-size: 1.45vw;
      }
      ul li{
        &::before{
          top: 0.6em;
        }
      }
    }
    &__footer{
      margin-right: 10vw;
      margin-top: 2.2vw;
      padding-left: 2.7vw;
      font-size: 1.04vw;

      a{
        text-decoration-thickness: 2px;
        text-underline-offset: .3em;
      }
      strong{
        margin-bottom: 1vw;
        font-size: 1.19vw;
        line-height: 1.1;
      }
    }
  }
}
