.articles{
  margin-top: 6vw;

  &__slider{

    .swiper{
      overflow: visible;
    }
    .swiper-slide{
      width: 80vw;
      padding-right: 6vw;
      box-sizing: content-box;
    }
  }
  &__slide{
    display: flex;
    flex-direction: column;
    padding: 6vw;
    border-radius: 6vw;
    background: #EDF3F5;

    &:hover{

      .link-diag{
        background: var(--color-2);
      }
    }
  }
  &__pic{
    display: block;
    margin-bottom: 4vw;
    position: relative;
    overflow: hidden;
    border-radius: 4vw;

    &::after{
      content: '';
      display: block;
      padding-bottom: 28200/460%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__title{
    display: block;
    margin-bottom: 1em;
    font-weight: 700;
    line-height: 1.25;
    font-size: 1.2em;
    height: 2*1.25em;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;  
    overflow: hidden;
  }
  &__excerpt{
    display: block;
    height: 1.55*3em;
    font-size: 0.8em;
    line-height: 1.55;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;  
    overflow: hidden;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__publish{
    display: block;
    color: #8B8B8B;
    font-size: 0.8em;
    margin-top: 1.5em;
  }
  &__btns{
    margin-top: 10vw;
    text-align: center;
  }
}


@media (min-width: @w-lg) {
	.articles{
    margin-top: 3.8vw;

    &__slider{

      .swiper-wrapper{
        justify-content: space-between;
      }
      .swiper-slide{
        padding: 0;
        width: 54000/1920vw;
      }
    }
    &__slide{
      border-radius: 2.08vw;
      padding: 2.08vw 2.08vw 2.08vw;
    }
    &__pic{
      margin-bottom: 1.7vw;
      border-radius: 1.56vw;
    }
    &__title{
      margin-bottom: 1.3vw;
      font-size: 3200/1920vw;
    }
    &__excerpt{
      margin-right: 3vw;
      font-size: 0.94vw;
    }
    &__publish{
      font-size: 0.83vw;
    }
    &__btns{
      margin-top: 2.6vw;
    }
    .link-diag{
      width: 3.12vw;
    }
  }
}
