.delivery{
  padding-bottom: 12vw;

  &__box-1{
    margin-top: 6vw;
  }
  &__wrap{
    margin-top: 6vw;
    padding: 8vw 6vw;
    border-radius: 6vw;
    background: #EDF3F5;

    .ui-selectmenu-button.ui-button{
      background: #FDFDFF;
      border: 0;
    }
  }

  &__item{
    &+&{
      margin-top: 10vw;
    }
  }
  &__col-1-1{
    margin-bottom: 6vw;

    .form__box-select{
      margin-bottom: 0;
    }
  }

  &__block{
    margin-top: 6vw;
    background: #FDFDFF;
    padding: 6vw;
    border-radius: 6vw;
  }
  &__logo{
    display: flex;
    margin-bottom: 4vw;
    align-items: center;
    justify-content: center;
    width: 50vw;
    height: 15vw;
    border: 2px solid #D9E7EB;
    border-radius: 2vw;
    padding: 2vw;

    img{
      max-width: 90%;
      max-height: 90%;
    }
  }
  &__name{
    display: block;
    font-weight: 700;
    font-size: 1.4em;
  }
  &__box{
    margin-top: 6vw;
  }
  &__list{
    margin-top: 3vw;
    font-weight: 300;
    font-size: 0.8em;

    li{
      margin-bottom: 2vw;
      display: flex;
      align-items: center;

      &::before{
        content: '';
        display: block;
        width: 16/18em;
        flex: 0 0 16/18em;
        height: 13/18em;
        margin-right: 0.5em;
        background: url(../img/delivery/item-1.svg) center / contain no-repeat;
      }
    }
  }
  &__txt{
    margin-top: 4vw;
    font-size: 0.8em;
    line-height: 1.36;
  }
  &__label{
    display: block;
    font-weight: 600;
  }
  &__bottom{
    margin-top: 12vw;
  }
  &__btns{
    text-align: center;
  }
  &__sub{
    margin-top: 4vw;
    font-size: 0.8em;
    color: #7B868F;
    line-height: 1.6;
  }
}


@media (min-width: @w-lg) {
	.delivery{
    padding-bottom: 6.3vw;

    &__box-1{
      margin-top: 2.1vw;
    }

    &__wrap{
      margin-top: 2.5vw;
      border-radius: 3500/1920vw;
      padding: 3.6vw 3vw 4.1vw 3.1vw;
    }
    &__row-1{
      display: flex;
    }
    &__col-1-1{
      margin-bottom: 0;
      margin-right: 3.12vw;
      width: 41800/1920vw;
    }

    &__block{
      margin-top: 1.6vw;
      max-width: 124600/1920vw;
      border-radius: 1vw;
      padding: 2.3vw 2.9vw 2.5vw 3vw;
    }
    &__items{
      height: 15.3vw;
      overflow-y: scroll;
      padding-right: 2vw;

      &::-webkit-scrollbar-track{
        background-color: #CFF2DD;
        border-radius: 7px;
      }
      &::-webkit-scrollbar{
        border-radius: 7px;
        width:14px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 7px;
        background-color: var(--color-main);
      }
    
      /* for Firefox */
      scrollbar-color: var(--color-main) #CFF2DD;
      scrollbar-width: thin;
    }
    &__item{
      &+&{
        margin-top: 3vw;
      }
    }

    &__logo{
      margin-bottom: .85vw;
      width: 18400/1920vw;
      height: 6600/1920vw;
      border-radius: .88vw;
      padding: .5vw;
    }
    &__name{
      font-size: 1.45vw;
    }
    &__item{
      display: flex;
      flex-wrap: wrap;
    }
    &__col-2-0{
      width: 100%;
      
    }
    &__col-2-1{

      &:nth-child(2){
        width: 50%;
      }
      &:nth-child(3){
        width: 46.1%;
        margin-left: auto;
        // padding-top: 4.4vw;

        // .delivery__box{
        //   margin-top: 0;
        // }
      }

    }
    &__box{
      margin-top: .7vw;
    }
    &__label{
      font-size: 1.09vw;
    }
    &__list{
      margin-top: .7vw;
      font-size: .94vw;

      li{
        margin-bottom: 0.5vw;
      }
    }
    &__txt{
      margin-top: .8vw;
      font-size: 0.94vw;
    }

    &__bottom{
      margin-top: 2.6vw;
      display: flex;
      align-items: center;
    }
    &__sub{
      margin-top: 0;
      margin-left: 2.6vw;
      font-size: 0.83vw;
      width: 47500/1920vw;
    }
  }
}
