.btn1{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 28800/360vw;
  height: 6400/360vw;
  padding: .2em 1.2em .2em;
  border: none;
  border-radius: 1300/360vw;
  cursor: pointer;
  outline: none;
  background: var(--color-main);
  text-align: center;
  text-decoration: none;
  color: #fff;
  font-family: var(--font-averta);
  font-weight: 700;
  font-size: 4.2vw;
  line-height: 1.1;
  transition-duration: .4s;
  transition-property: background;
  box-shadow: 1.3vw 1.3vw 2.6vw rgba(0, 0, 0, 0.15);

  .lg-min({
    min-width: 44300/1920vw;
    height: 9700/1920vw;
    border-radius: 1.04vw;
    font-size: 2800/1920vw;
    padding: .2em 1.8em .2em;
  });

  &:not([disabled]):hover{
    background: var(--color-2);
    text-decoration: none;

    .btn1__arr{
      fill: #C08B22;
    }
  }
  &[disabled]{
    opacity: 0.5;
    cursor: not-allowed;
  }

  &__arr{
    width: 40/28em;
    display: block;
    margin-left: 1em;
    fill: #A9EFC0;
    transition: 0.3s fill;

    .lg-min({
      margin-left: 44/28em;
    });

    &&_back{
      margin-left: 0;
      margin-right: 44/28em;
      transform: rotate(180deg);
    }
  }
  &__safe{
    display: block;
    width: 1em;
    margin-left: 0.5em;
    
    svg{
      fill: currentColor;
    }
  }
  &__safe2{
    display: block;
    width: 1em;
    margin-left: 0.5em;
    
    svg{
      stroke: currentColor;
    }
  }
  &__plus{
    display: block;
    width: .82em;
    margin-left: 0.8em;
    margin-top: 0.2em;
    
    svg{
      fill: currentColor;
    }
  }

  &&_v2{
    background: #C6F2D5 !important;
    color: var(--color-main);
    box-shadow: none;

    .btn1__arr{
      margin-left: .3em;

      svg{
        stroke: var(--color-main) !important;
      }
    }
  }
  &&_small{
    
    .lg-min({
      min-width: 32000/1920vw;
      height: 6400/1920vw;
      border-radius: .83vw;
      font-size: 1.04vw;
      padding: 0.2em 1.4em 0.2em;
    });

    .btn1__arr{
      .lg-min({
        width: 1.09vw;
        margin-left: 0.5em;
      })
    }
  }
}

.btn2{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 28800/360vw;
  min-height: 6000/360vw;
  padding: .2em 1.8em .2em;
  border: 2px solid var(--color-main);
  border-radius: 1300/360vw;
  cursor: pointer;
  outline: none;
  background: transparent;
  text-align: center;
  text-decoration: none;
  color: var(--color-main);
  font-family: var(--font-averta);
  font-weight: 600;
  font-size: 1600/360vw;
  line-height: 1.1;
  transition-duration: .4s;
  transition-property: background, color;

  .lg-min({
    min-width: 45700/1920vw;
    min-height: 7600/1920vw;
    border-radius: 1.04vw;
    font-size: 1.04vw;
  });

  &&_big{
    .lg-min({
      min-height: 9800/1920vw;
      font-size: 1.46vw;
    });
  }
  &&_small{
    min-width: 1px;

    .lg-min({
      min-height: 3.4vw;
      border-radius: .83vw
    });
  }
  &&_red{
    border-color: var(--color-3);
    color: var(--color-3);

    &:hover{
      background: var(--color-3);
      color: #fff;
    }
  }

  &:hover{
    background: var(--color-main);
    color: #fff;
    text-decoration: none;
  }

  &__icon{
    display: block;
    width: 14/14em;
    margin-left: .8em;
    margin-top: 0.1em;

    svg{
      fill: currentColor;
    }
  }
}

.btn3{
  display: inline-flex;
