    }
  }
}

.circle-btn{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4000/360vw;
  height: 4000/360vw;
  border-radius: 50%;
  padding: 0;
  border: 0;
  background: #fff;
  border: 0;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 27px rgba(0, 0, 0, 0.10);
  color: #000;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    width: 5000/1920vw;
    height: 5000/1920vw;
  });

  &.a svg{
    transform: translateY(5%);
  }

  &:hover{
    background: var(--color-main);
    color: #fff;

    &[data-count]::after,
    &[data-status]::after{
      background: var(--color-2);
    }
  }

  &[data-count]::after{
    content: attr(data-count);
    width: 50%;
    height: 50%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    padding-bottom: 0.1em;
    top: -18%;
    right: -18%;
    background: var(--color-main);
    color: #fff;
    font-weight: 700;
    font-size: 1200/360vw;
    transition: 0.3s color, 0.3s background;
    line-height: 0.7;
    box-sizing: border-box;

    .lg-min({
      font-size: 1200/1920vw;
    });
  }
  &[data-sdf="1"]{
    svg{
      stroke: currentColor;
    }
  }
  &[data-sdf="2"]{
    svg{
      width: 44%;
    }
  }
  
  &[data-status]::after{
    content: '';
    display: block;
    position: absolute;
    width: 800/360vw;
    height: 800/360vw;
    top: 0%;
    right: 5%;
    border-radius: 50%;
    background: var(--color-curr, transparent);
    transition: 0.2s background;

    .lg-min({
      width: .63vw;
      height: 0.63vw;
      border-radius: .315vw;
    });
  }
  &[data-status="warn"]{
    --color-curr: var(--color-main);
  }

  svg{
    position: relative;
    width: 50%;
    fill: currentColor;

    .lg-min({
      width: 2800/50%;
    });
  }
}

.square-btn{
  display: block;
  width: 3000/360vw;
  background: #fff;
  border-radius: 1vw;
  box-shadow: 0vw .1vw 1.56vw 0vw rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: 0.3s background;
  padding: 0;
  border: 0;
  outline: none;

  .lg-min({
    width: 4000/1920vw;
    border-radius: .52vw;
  });

  svg{
    display: block;
    transition: 0.2s fill, .3s stroke;
  }

  &[data-type-btn="favorite"]{
    svg{
      fill: transparent;
      stroke: #FF005C;
    }

    &[data-check="true"] svg,
    &:hover svg{
      fill: #FF005C;
    }
  }
  &[data-type-btn="share"]{
    svg{
      fill: #1AC954;
      stroke: none;
    }

    &:hover{
      background: var(--color-main);

      svg{
        fill: #fff;
      }
    }
  }
  &[data-type-btn="default"]{

    &:hover{
      background: var(--color-main);

      svg{
        fill: #fff;
      }
    }
  }
  &[data-type-btn="favorite-fill"]{
    svg{
      fill: #FF005C;
    }

    &:hover{
      background: var(--color-main);

      svg{
        fill: #fff;
      }
    }
  }
  &[data-type-btn="stroke-green"]{

    svg{
      stroke: var(--color-main);
    }

    &:hover{
      background: var(--color-main);

      svg{
        stroke: #fff;
      }
    }
  }
  &[data-type-btn="stroke-red"]{

    svg{
      stroke: #FF3030;
    }

    &:hover{
      background: #FF3030;

      svg{
        stroke: #fff;
      }
    }
  }

  &&_big{
    .lg-min({
      width: 5700/1920vw;
    });
  }
}


.social-link{
  position: relative;
  display: block;
  width: 3000/360vw;

  .lg-min({
    width: 2.1vw;
  });

  &[data-title]::after{
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    opacity: 0;
    top: 0;
    left: 0;
    transition: 0.3s opacity, 0.3s transform;
    background: var(--color-curr);
    pointer-events: none;
  }
  &[data-title="Telegram"]::after{
    --color-curr: rgba(54, 173, 225, 0.20);
  }
  &[data-title="WhatsApp"]::after{
    --color-curr: rgba(13, 193, 67, 0.20);
  }

  &:hover{
    &::after{
      opacity: 1;
      transform: scale(56/40);
    }
  }

  svg{
    display: block;
    position: relative;
    z-index: 1;
    overflow: visible;
  }
}

.box-phones{
  position: relative;

  &__label{
    display: inline-flex;
    align-items: center;
    padding: 0.7em 0.75em 0.6em;
    border-radius: 1.5em;
    background: #F0EFF3;
    font-weight: 600;
    font-size: 1200/360vw;
    white-space: nowrap;

    .lg-min({
      padding: 0.45em .75em;
      font-size: 0.94vw;
      background: #F0EFF3;
    });

    &::after{
      content: '';
      display: block;
      width: 12/18em;
      flex: 0 0 12/18em;
      height: 6/18em;
      margin-left: 1em;
      background: url(../img/svg/dropdown-1-black.svg) center / contain no-repeat;
    }

    i{
      display: block;
      width: 1.2em;
      flex: 0 0 1.2em;
      margin-right: 0.4em;
    }
  }
  &__list{
    li{
      padding: .4em 0;

      a{
        display: inline-block;
        white-space: nowrap;
        transition: 0.3s color;

        &:hover{
          color: var(--color-main);
        }
      }
    }

    &&_aside{

      .lg({
        font-size: 1600/360vw;
      });
    }
  }
}

.cart-btn{
  display: flex;
  align-items: center;
  padding: .4em .4em .4em 1.1em;
  border-radius: 1.5em;
  font-size: 1400/360vw;
  font-weight: 700;
  box-shadow: 3px 3px 30px 0px rgba(0, 0, 0, 0.10);
  background: #fff;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    padding: .4em .4em .4em 1.1em;
    height: 2.6vw;
    font-size: 0.94vw;
  });
  
  &:hover{
    background: var(--color-main);
    color: #fff;

    &>small{
      background: var(--color-2);
    }
  }

  &>i{
    display: block;
    width: 1.2em;
    margin-right: 0.8em;

    svg{
      fill: currentColor;
      stroke: currentColor;
    }
  }
  &>small{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1.65em;
    width: 2em;
    height: 2em;
    background: var(--color-main);
    color: #fff;
    border-radius: 50%;
    font-weight: 600;
    font-size: 1em;
    transition: 0.3s color, 0.3s background;
  }
}

.profile-btn{
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.75em 1.9em 0.7em 1.3em;
  border-radius: 1.5em;
  font-size: 1400/360vw;
  font-weight: 700;
  box-shadow: 3px 3px 30px 0px rgba(0, 0, 0, 0.10);
  background: #fff;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    height: 2.6vw;
    font-size: 0.94vw;
    padding: 0.4em 2.1em 0.4em 1.3em;
  });

  &[data-status]::after{
    content: '';
    display: block;
    position: absolute;
    width: .95em;
    height: .95em;
    top: -5%;
    right: 0;
    margin-right: 8%;
    border-radius: 50%;
    background: var(--color-curr, transparent);
    transition: 0.2s background;
  }
  &[data-status="warn"]{
    --color-curr: var(--color-main);
  }
  
  &:hover{
    background: var(--color-main);
    color: #fff;

    &[data-status]::after{
      background: var(--color-2);
    }
  }

  &>i{
    display: block;
    width: 1.2em;
    margin-right: 0.8em;

    svg{
      fill: currentColor;
    }
  }
}


