.ui-tabs .tabs-2{
  border-radius: 2vw;
  background: #D1F4D8;
  padding: 0;

  .ui-tabs-tab{
    float: none;

    &::before,
    &::after{
      display: none;
    }

    &.ui-tabs-active{

      .ui-tabs-anchor{
        background: var(--color-main);
        color: #fff;
        box-shadow: 8px 8px 16px 0px rgba(0, 151, 50, 0.24);
        opacity: 1;
      }
    }
    
    .ui-tabs-anchor{
      display: flex;
      align-items: center;
      padding: 0.6em 1.5em 0.65em;
      color: var(--color-main);
      font-weight: 700;
      float: none;
      border-radius: 2vw;
      opacity: 0.5;
      transition: 0.3s opacity;

      &:hover{
        opacity: 1;
      }

      &>i{
        display: block;
        width: 6vw;
        flex: 0 0 6vw;
        margin-right: 3vw;

        svg{
          fill: currentColor;
        }
      }
    }
  }
}


@media (min-width: @w-lg) {
	.ui-tabs .tabs-2{
    display: flex;
    border-radius: .62vw;

    .ui-tabs-tab{
      flex :1;

      .ui-tabs-anchor{
        justify-content: center;
        font-size: 1.04vw;
        border-radius: .62vw;

        &>i{
          width: 1.25vw;
          flex-basis: 1.25vw;
          margin-right: 0.83vw;
        }
      }
    }
  }
}
