.sales{
  margin-top: 6vw;

  .swiper-slide{
    opacity: 0;
    height: auto;
    transition-property: transform, opacity;
    transition-duration: 0.3s;

    &.swiper-slide-active{
      opacity: 1;
    }
  }

  &__slide{
    position: relative;
    height: 100%;
    background: #EDF3F5;
    border-radius: 6vw;
    overflow: hidden;
  }
  &__pic{
    position: relative;

    &::after{
      content: '';
      display: block;
      padding-bottom: 37200/404%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__desc{
    position: relative;
    height: 100%;
    padding: 6vw;
  }
  &__title{
    display: block;
    font-weight: 700;
    font-size: 1.4em;
    color: var(--color-main);
  }
  &__txt{
    margin-top: 3vw;
    font-weight: 700;
    line-height: 35/26;

    strong{
      color: var(--color-main);
    }
  }
  &__sub{
    margin-top: 3vw;
    font-size: 0.8em;
    line-height: 28/19;
  }
  &__btn{
    margin-top: 5vw;
    min-width: 1px;
  }
}


@media (min-width: @w-lg) {
	.sales{
    margin-top: 3.5vw;
    margin-right: -5800/1920vw;

    .swiper-slide{
      padding-right: 5800/1920vw;
      opacity: 1;
    }
    &__nav{
      display: none;
    }
    &__slide{
      display: flex;
      border-radius: 2.08vw;
    }
    &__pic{
      width: 40400/1920vw;
      flex: 0 0 40400/1920vw;
      order: 2;
    }
    &__desc{
      flex: 1;
      padding: 1.6vw 2.1vw 2vw;
    }
    &__title{
      font-size: 5000/1920vw;
    }
    &__txt{
      margin-top: .6vw;
      font-size: 1.35vw;
    }
    &__sub{
      margin-top: 1.6vw;
      font-size: 1900/1920vw;
    }
    &__btn{
      margin-top: 2.4vw;
      padding-left: 2.4em;
      padding-right: 2.4em;
    }
  }
}
