@media (min-width:1070px) {
  .search{
    position: relative;
    display: flex;
    flex: 1;
    height: 5000/1920vw;
    border-radius: 1.25vw;
    background: #f6f6f7;
    border: 1px solid #f6f6f7;
    transition: 0.3s border-color;
    padding-left: 0.5vw;
  
    &:hover{
      border-color: var(--color-main);
    }

    &__submit{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2.6vw;
      height: 2.6vw;
      margin-right: 0.2em;
      padding: 0;
      border: 0;
      background: none;
      outline: none;
      cursor: pointer;
  
      svg{
        display: block;
        width: 2400/50%;
      }
    }
    &__input{
      display: block;
      flex: 1;
      background: none;
      border: 0;
      outline: none;
      font-family: var(--font-averta);
      font-weight: 400;
      color: #000;
      padding: .1em 1em .2em .4em;
      font-size: 0.94vw;
  
      &::placeholder{
        color: #9CA5AC;
      }
    }
    &__wrap{
      display: none;
      padding: 1vw 1.1vw;
      width: 100%;
      position: absolute;
      z-index: 10;
      left: 0;
      top: 100%;
      margin-top: 0.52vw;
      border-radius: .73vw;
      background: rgb(246, 246, 247);
      
      &:hover{
        display: block;
      }
    }
    &__empty{
      display: block;
      text-align: center;
      color: rgb(139, 139, 139);
      text-align: center;
      padding: .52vw 0;
      font-size: 0.94vw;
    }
    &__row{
      margin-bottom: 2rem;
      display: flex;

      &:last-child{
        margin-bottom: 0;
      }
    }
    &__bar{
      padding-right: 1vw;
      max-height: 60vh;
      overflow: auto;

      &::-webkit-scrollbar-track{
        border-radius: 3px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar{
        width: 6px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 3px;
        background-color: var(--color-main);
      }
    
      /* for Firefox */
      // scrollbar-color: var(--color-main) #CFF2DD;
      // scrollbar-width: thin;
    }
    &__category{
      display: block;
      padding-right: 3vw;
      font-weight: 700;
      font-size: 0.73vw;
      color: #9ca5ac;
    }
    &__item{
      margin: 0 0 1vw;
      font-size: .94vw;

      &:last-child{
        margin-bottom: 0;
      }
  
      a{
        display: inline-block;
        transition: 0.3s color, 0.3s text-decoration-color;
        text-decoration: underline;
        text-decoration-thickness: 1px;
        text-decoration-color: transparent;
        text-underline-offset: 0.2em;
  
        &:hover{
          color: var(--color-main);
          text-decoration-color: currentColor;
          // font-weight: 700;
        }
      }
    }
    &__block{
      margin-bottom: 1vw;
      display: flex;
      align-items: center;
      font-size: .94vw;

      &-img{
        display: block;
        align-self: flex-start;
        width: 5800/1920vw;
        flex: 0 0 5800/1920vw;
        border-radius: 700/1920vw;
        overflow: hidden;
        margin-right: 0.53vw;

        img{
          aspect-ratio: ~'58/40';
          object-fit: cover;
        }
      }
      &-sub{
        display: block;
        font-weight: 700;
        color: var(--color-main);
        font-size: 0.73vw;
      }
      
      a{
        display: inline-block;
        transition: 0.3s color, 0.3s text-decoration-color;
        text-decoration: underline;
        text-decoration-thickness: 1px;
        text-decoration-color: transparent;
        text-underline-offset: 0.2em;
  
        &:hover{
          color: var(--color-main);
          text-decoration-color: currentColor;
          // font-weight: 700;
        }
      }
    }
    &__list{
      flex: 1;

      // &::-webkit-scrollbar-track{
      //   border-radius: 3px;
      //   background-color: #CFF2DD;
      // }
      // &::-webkit-scrollbar{
      //   width: 6px;
      //   background-color: #CFF2DD;
      // }
      // &::-webkit-scrollbar-thumb{
      //   border-radius: 3px;
      //   background-color: var(--color-main);
      // }
    
      // /* for Firefox */
      // scrollbar-color: var(--color-main) #CFF2DD;
      // scrollbar-width: thin;
    }
  }
}
