.breadcrumbs{
  display: flex;
  margin-bottom: 6vw;
  flex-wrap: wrap;
  font-size: 1200/360vw;
  color: #8B8B8B;

  li{
    margin-right: 0.6em;

    &:not(:last-child)::after{
      content: '';
      display: inline-block;
      width: 14/16em;
      height: 14/16em;
      margin-left: 0.4em;
      background: url(../img/svg/bread.svg) center / contain no-repeat;
    }

    &:last-child{
      color: #000;
      font-weight: 600;
    }

    a{
      display: inline-block;
      transition: 0.3s color;

      &:hover{
        color: var(--color-main);
      }
    }
    span{
      display: inline;
      border-bottom: 1px solid;
    }
  }
}


@media (min-width: @w-lg) {
	.breadcrumbs{
    margin: 0.8vw 0 2.65vw;
    font-size: .83vw;
  }
}
