.filters{
  position: relative;
  
  &__wrapper{
    margin-top: 4vw;
    display: none;
  }
  &__head{
    margin-bottom: 6vw;
    display: flex;
    align-items: center;
  }
  &__title{
    display: block;
    margin-right: auto;
    font-weight: 700;
    line-height: 1.1;
    font-size: 1.2em;
  }


  &__header{
    display: block;
    width: 100%;
    font-weight: 700;
    margin-bottom: 3vw;
  }
  &__prices{
    margin-bottom: 10vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 1em;

    &-box{
      display: flex;
      align-items: center;
      width: 48%;
      height: 12vw;
      border-radius: 2vw;
      background: #F6F6F7;
      padding-top: 0.2em;
      border: 1px solid #F6F6F7;
      transition: 0.3s border-color;

      &:hover,
      &:focus-within{
        border-color: var(--color-main);
      }
    }
    &-s{
      display: block;
      padding-left: 1em;
      padding-right: 0.7em;
      color: #8b8b8b;
    }
    &-inp{
      display: block;
      flex: 1;
      width: 5em;
      background: none;
      border: 0;
      outline: none;
      font-size: inherit;
      font-family: var(--font-averta);
      font-weight: 400;
      color: #000;
      padding: 0;

      &::placeholder{
        color: #8b8b8b;
      }
    }
    &-scale{
      margin-top: 8vw;
      width: 100%;
    }
  }

  &__search{
    display: block;
    margin-bottom: 4vw;
    position: relative;
    background: #f6f6f7;
    border-radius: 2vw;

    &-inp{
      display: block;
      position: relative;
      z-index: 1;
      width: 100%;
      height: 12vw;
      font-size: 1em;
      border: 0;
      outline: none;
      font-family: var(--font-averta);
      font-weight: 400;
      padding: .2em 10vw .3em 1em;
      background: none;
    }
    &-btn{
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      z-index: 5;
      width: 12vw;
      height: 100%;
      top: 0;
      right: 0;
      background: none;
      border: 0;
      outline: none;
      cursor: pointer;

      svg{
        width: 54%;
      }
    }
  }

  &__checkboxes{

    &-hide{
      display: none;
    }
  }
  &__checkbox,
  &__radio{
    display: flex;
    align-items: center;
    width: max-content;
    margin-bottom: 2vw;
    position: relative;
    cursor: pointer;

    // &:last-child{
    //   margin-bottom: 0;
    // }

    &:hover input:not(:checked)+.filters__checkbox-check::after,
    &:hover input:not(:checked)+.filters__radio-check::after{
      background-image: url(../img/svg/checkbox-2-green.svg);
      opacity: 0.4;
    }
    &:hover input:not(:checked)~.filters__checkbox-txt,
    &:hover input:not(:checked)~.filters__radio-txt{
      font-weight: 600;
    }

    input{
      position: absolute;
      left: -9999px;

      &:checked{
        &+.filters__checkbox-check,
        &+.filters__radio-check{
          background: var(--color-main);

          &::after{
            opacity: 1;
          }
        }
      }
    }

    &-check{
      display: block;
      width: 4vw;
      flex: 0 0 4vw;
      margin-right: 2vw;
      border-radius: 1vw;
      border: 1px solid var(--color-main);
      transition: 0.2s background;

      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
        background: url(../img/svg/checkbox-2.svg) center / contain no-repeat;
        opacity: 0;
        transition: 0.2s opacity;
      }
    }
    &-txt{
      display: block;

      i{
        display: inline-block;
        margin-left: 0.4em;
        color: #8b8b8b;
        font-style: normal;
      }
    }
  }
  &__more{
    margin-top: 4vw;
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    color: var(--color-main);

    &.active svg{
      transform: rotate(180deg);
    }

    svg{
      display: block;
      margin-left: 0.7em;
      margin-top: 0.2em;
      width: 14/22em;
      stroke: currentColor;
    }
  }
  &__mobile-collapse{
    display: inline-flex;
    align-items: center;
    font-weight: 700;

    &.active svg{
      transform: rotate(180deg);
    }

    svg{
      display: block;
      margin-left: 0.7em;
      margin-top: 0.2em;
      width: 14/22em;
      stroke: currentColor;
    }
  }
  &__radio{
    &-check{
      border-radius: 50% !important;
    }
  }

  &__box{
    // margin-top: 6vw;
    padding-top: 6vw;

    &&_first{
      padding-top: 0;
      margin-bottom: 10vw;
    }

    &-label{
      display: flex;
      align-items: center;
      cursor: pointer;
      user-select: none;

      &.active::after{
        transform: rotate(0deg);
      }
      @media(hover:hover){
        &:hover::after{
          transform: rotate(-90deg);
        }
      }

      &::after{
        content: '';
        display: block;
        width: 34/26em;
        flex: 0 0 34/26em;
        height: 34/26em;
        margin-left: 1em;
        border-radius: 50%;
        background: url(../img/svg/dropdown-3.svg) center / contain no-repeat, rgba(26, 201, 84, 0.2);
        transform: rotate(-180deg);
        transition: 0.3s transform;
      }
      span{
        display: block;
        flex: 1;
      }
    }
    &-wrap{
      display: none;
    }
  }
}


@media (min-width: @w-lg) {
	.filters{

    &__mobile-collapse{
      display: none;
    }
    &__wrapper{
      display: block;
      margin-top: 0;
      padding-top: 0.15vw;
    }
    &__head{
      padding-right: 0.65vw;
      margin-bottom: 1.7vw;
    }
    &__clear{
      margin-top: 0.5vw;
    }
    
    &__title{
      font-size: 3600/1920vw;
    }
    &__header{
      margin-bottom: 1vw;
      font-size: 1.35vw;
    }
    &__prices{
      margin-bottom: 3vw;
      font-size: 1.15vw;

      &-box{
        height: 6800/1920vw;
        border-radius: .73vw;
      }
      &-scale{
        margin-top: 2vw;
      }
    }
    &__search{
      margin-bottom: 1.3vw;
      border-radius: .73vw;

      &-inp{
        height: 6800/1920vw;
        font-size: 1.15vw;
      }
      &-btn{
        width: 6800/1920vw;
      }
    }

    &__checkboxes{
    }
    &__checkbox,
    &__radio{
      margin-bottom: 1vw;
      font-size: 1.15vw;

      &-check{
        width: 1.35vw;
        flex-basis: 1.35vw;
        margin-right: .63vw;
        border-radius: .3vw;
      }
    }

    &__more{
      margin-top: 0;
      font-size: 1.15vw;
    }

    &__box{
      // margin-top: 2.2vw;
      padding-top: 2.2vw;

      &&_first{
        margin-bottom: 3vw;
      }

      &-label{
        margin-bottom: 1.2vw;
      }
    }
  }
}
