.menu{
	&__item{
		margin-bottom: 3.05vh;
	}
	&__link{
		display: inline-block;
		transition: 0.3s color;
		font-size: 2000/360vw;

		&:hover,
		&.active{
			color: var(--color-main);
			font-weight: 600;
		}
	}
}


@media (min-width: @w-lg) {
	.menu{
		display: flex;
		justify-content: flex-end;
		margin: 0 -1.3vw;

		&__item{
			margin: 0 1.3vw;
		}
		&__link{
			color: #9CA5AC;
			font-size: 0.83vw;
		}

	}
}