.asort{
  margin-top: 6vw;

  &__pic{
    position: relative;
    overflow: hidden;
    border-radius: 4vw;
  }
  &__pic-label{
    padding: 4vw 6vw;
    border-radius: 4vw;
    background: #EDF3F5;
    display: flex;
    align-items: center;
    line-height: 1.4;
    font-size: 0.8em;

    &>i{
      display: block;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 4vw;
    }
  }
  &__desc{
    margin-top: 6vw;
  }
  &__txt{
    margin-bottom: 2.7vw;
    line-height: 1.4;
  }
  &__list{
    line-height: 1.45;

    li{
      margin-bottom: 4vw;
      display: flex;
      align-items: center;

      &>i{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 4vw;
        width: 14vw;
        flex: 0 0 14vw;
        height: 14vw;
        background: #fff;
        border-radius: 50%;
        box-shadow: .73vw .73vw 2.08vw rgba(56, 106, 94, 0.16);

        img,svg{
          width: 4800/70%;
        }
      }
    }
  }

  &__collapse{
    line-height: 1.4;

    &-wrap{
      margin-top: 0;
      display: grid;
      grid-template-rows: 0fr;
      overflow: hidden;
      transition: 0.3s grid-template-rows, 0.3s margin;

      &.active{
        margin-bottom: 4vw;
        grid-template-rows: 1fr;
      }

      &>div{
        min-height: 0px;
      }
    }
  }
  &__btns{
    margin-top: 10vw;
  }
}


@media (min-width: @w-lg) {
	.asort{
    margin-top: 3.2vw;
    display: flex;
    align-items: flex-start;

    &__pics{
      order: 2;
      position: relative;
      width: 77700/1920vw;
    }
    &__pic{
      border-radius: 2.08vw;
      position: relative;
      z-index: 1;
    }
    &__pic-label{
      position: absolute;
      z-index: 0;
      bottom: -7.7vw;
      width: 100%;
      left: -8.4vw;
      padding: 2.7vw 2.4vw 2.2vw;
      font-size: 1.25vw;
      border-radius: 2.08vw;

      &>i{
        width: 8300/1920vw;
        flex-basis: 8300/1920vw;
        margin-right: 3200/1920vw;
      }
    }
    &__desc{
      width: 85000/1920vw;
      margin: 0 auto 0 0;
    }
    &__txt{
      margin-bottom: 2.7vw;
      font-size: 1.25vw;
      max-width: 65000/1920vw;
    }
    &__list{
      display: flex;
      flex-wrap: wrap;
      font-size: 1.25vw;

      li{
        width: 26.5vw;
        margin-bottom: 2.6vw;
        padding-right: 2vw;

        &:nth-child(even){
          width: 17vw;
          padding-right: 0;
        }

        &>i{
          width: 7000/1920vw;
          height: 7000/1920vw;
          flex: 0 0 7000/1920vw;
          margin-right: 1.15vw;
        }
      }
    }
    &__collapse{
      margin-top:.6vw;
      margin-right: 7vw;
      font-size: 1.25vw;

      &-wrap.active{
        margin-bottom: .5vw;
      }
    }
    &__btns{
      margin-top: 3.2vw;
    }
  }
}
