.form{

  &__box-1{
    margin-bottom: 6vw;

    .lg-min({
      margin-bottom: 1.3vw;
    });
  }
  &__box-2{
    margin-bottom: 6vw;

    .lg-min({
      margin-bottom: .83vw;
    });
  }
  &__box-input{
    position: relative;
    
    .lg-min({
      max-width: 36400/1920vw;
    });
  }
  &__box-select{
    position: relative;
    margin-bottom: 8vw;

    .lg-min({
      max-width: 49800/1920vw;
      margin-bottom: 1.5vw;
    });
  }
  &__box-submit-1{
    position: relative;
    margin-top: 12vw;

    .lg-min({
      margin-top: 7200/1920vw;
    });
  }
  &__box-submit-2{
    position: relative;
    margin-top: 12vw;

    .lg-min({
      margin-top:5000/1920vw;
    });

    .btn1{

      .lg-min({
        min-width: 25000/1920vw;
      });
    }
  }
  &__input,
  &__select{
    width: 100%;
    height: 16vw;
    display: inline-block;
    color: #000;
    padding: .2em 1.3em .3em;
    border: 2px solid #F5FBFC;
    border-radius: 2vw;
    background: #F5FBFC;
    font-family: var(--font-averta);
    outline: none;
    font-size: 1em;
    transition: 0.3s border-color;

    .lg-min({
      height: 7700/1920vw;
      border-radius: .73vw;
      font-size: 1.04vw;
    });


    &:hover{
      border-color: var(--color-main);
    }

    &::placeholder{
      color: #8B8B8B;
    }
  }
  &__input{
    &&_v2{
      border: 1px solid #b4c0bd;
      background: transparent;
    }
    &.err{
      border-color: red;
    }
  }
  
  &__box-textarea{
    position: relative;
    overflow: hidden;
    border-radius: 2vw;
    padding: 4vw;
    border: 2px solid #F5FBFC;

    .lg-min({
      padding: 2.2vw 1vw 2vw 2.4vw;
      border-radius: 3500/1920vw;
    });

    &&_v2{
      border: 1px solid #b4c0bd;
    }
    &&_v3{
      border: 1px solid #b4c0bd;

      .lg-min({
        padding-top: 1vw;
        padding-left: 1.5vw;
        
      });
    }
  }
  &__textarea{
    width: 100%;
    height: 15vh;
    display: inline-block;
    color: #000;
    padding: 0;
    border: 0;
    background: #F5FBFC;
    font-family: var(--font-averta);
    outline: none;
    font-size: 1em;
    transition: 0.3s border-color;
    resize: none;

    .lg-min({
      height: 11vw;
      font-size: 1.25vw;

      &::-webkit-scrollbar-track{
        background-color: transparent;
        outline: 1px solid var(--color-main);
        outline-offset: 1px;;
        border: 1px solid #d1f0dd;
        border-radius: 6px;
      }
      &::-webkit-scrollbar{
        width:12px;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 7px;
        background-color: var(--color-main);
        // border: 1px solid #CFF2DD;
      }
    
      /* for Firefox */
      scrollbar-color: var(--color-main) #CFF2DD;
      scrollbar-width: thin;
    });

    &&_v2{
      background: transparent;
    }
    &&_v3{
      background: transparent;
      
      .lg-min({
        height: 4.7vw;
        font-size: 1.04vw;
      })
    }


    &:hover{
      border-color: var(--color-main);
    }

    &::placeholder{
      color: #8B8B8B;
    }
  }

  &__submit{

    &[disabled]{
      cursor: not-allowed;
      opacity: .5;
    }
  }

  &__label{
    display: block;
    margin-bottom: 0.5em;

    .lg-min({
      font-size: .94vw;
    });

    &&_req::after{
      content: '*';
      color: #fe2c2c;
    }
  }

  &__policy{
    display: inline-flex;
    align-items: flex-start;
    position: relative;
    color: #565656;
    font-size: 0.8em;
    text-align: left;
    cursor: pointer;
    user-select: none;

    &-check{
      display: block;
      position: relative;
      top: 0.1em;
      width: 1em;
      flex: 0 0 1em;
      margin-right: 0.4em;
      background: url(../img/svg/policy-1.svg) center / contain no-repeat;

      svg{
        opacity: 0;
        transition: 0.3s opacity;
      }
    }

    input{
      position: absolute;
      left: -9999px;

      &:checked{
        &~.form__policy-check svg{
          opacity: 1;
        }
      }
    }
    &-txt{
      display: block;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-decoration-thickness: 1px;
      text-underline-offset: .2em;
    }
  }

  &__radios{
    &-item{
      margin-bottom: 4vw;

      .lg-min({
        margin-bottom: 0.52vw;
      });

      &:last-child{
        margin-bottom: 0;
      }
    }
    &-wrap{
      display: none;
      padding: 4vw 0 1px;
      font-size: 0.8em;

      .lg-min({
        padding: 0 0 1px 2vw;
        font-size: 1.04vw;
      });

      p,li{
        margin-bottom: 0.7em;
      }
      ul li{
        list-style: disc;
        margin-left: 1.1em;
      }
      a{
        text-decoration: underline;
        text-decoration-skip-ink: none;
        text-underline-offset: .2em;
      }
    }
  }
  &__radio{
    display: flex;
    align-items: flex-start;
    position: relative;
    cursor: pointer;
    font-size: 0.8em;

    .lg-min({
      font-size: 1.25vw;
    });

    &:hover{
      .form__radio-check{
        border-color: var(--color-main);
      }
    }

    input{
      position: absolute;
      left: -9999px;

      &:checked{
        &~.form__radio-txt{
          font-weight: 600;
        }
        &+.form__radio-check{
          background: var(--color-main);
          border-color: var(--color-main);

          &::after{
            opacity: 1;
          }
        }
      }
    }

    &-check{
      display: block;
      width: 4vw;
      flex: 0 0 4vw;
      margin-top: 0.1em;
      margin-right: 2vw;
      border-radius: 50%;
      border: 1px solid #CACACA;
      transition: 0.2s background, 0.2s border-color;

      .lg-min({
        width: 1.35vw;
        flex-basis: 1.35vw;
        margin-right: 0.4vw;
      });

      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
        background: url(../img/svg/checkbox-2.svg) center / contain no-repeat;
        opacity: 0;
        transition: 0.2s opacity;
      }
    }
    &-txt{
      display: block;
    }
    &-type{
      display: inline-block;
      width: 38/24em;
      vertical-align: middle;
      margin-left: 0.3em;
    }
  }


  &__row-1{
    .lg-min({
      display: flex;
      gap: 3200/1920vw;
    });


    &>div{
      .lg-min({
        margin: 0;
      });
    }
  }
  &__col-1-1{
    margin-bottom: 6vw;
  }
  &__col-1-2{
    .lg-min({
      padding-top: 1.8vw;
      width: 36400/1920vw;
    });
  }

  &__box-pass{
    position: relative;

    .form__input{
      position: relative;
      z-index: 1;
      padding-right: 14vw;

      .lg-min({
        padding-right: 4vw;
      });
    }
  }
  &__btn-pass{
    position: absolute;
    z-index: 5;
    right: 2vw;
    width: 8vw;
    top: 50%;
    padding: 0;
    transform: translate(0%, -50%);
    border: 0;
    background: none;
    cursor: pointer;
    outline: none;
    color: var(--color-main);
    transition: 0.3s color, 0.3s opacity;

    .lg-min({
      width: 3400/1920vw;
      right: 1.35vw;
    });

    &:hover{
      color: #000;
      opacity: 0.5;
    }
    &.active{
      .h{
        display: none;
      }
      .o{
        display: block;
      }
    }

    svg{
      fill: currentColor;
    }
    .o{
      display: none;
    }
  }
  &__box-forgot{
    text-align: right;
    margin-top: -4vw;

    .lg-min({
      margin-top: -0.2vw;
    });
  }
  &__forgot{
    display: inline-block;
    font-weight: 600;
    color: var(--color-main);
    border-bottom: 1px solid;
    transition: 0.3s color, 0.3s border-color;
    font-size: 0.8em;

    .lg-min({
      font-size: 0.73vw;
    });

    &:hover{
      color: #000;
      border-color: transparent;
    }
  }
}