.princ{
  margin-top: 6vw;

  &__desc{
    margin-top: 10vw;
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    font-size: 1.2em;
    font-weight: 700;
  }
  &__txt{
    line-height: 1.4;
  }
  &__pics{
    position: relative;
  }
  &__diplom-1{
    position: relative;
    margin-left: 20%;
    margin-bottom: 10%;
    z-index: 1;

    img{
      filter: drop-shadow(1vw 0px 2.5vw rgba(0, 0, 0, 0.08))
    }
  }
  &__diplom-2{
    position: absolute;
    z-index: 2;
    width: 80%;
    top: 5%;
    left: 0;

    img{
      filter: drop-shadow(1vw 0px 2.5vw rgba(0, 0, 0, 0.08))
    }
  }
  &__label{
    display: flex;
    align-items: center;
    justify-content: center;

    &>i{
      display: block;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 2vw;
    }
    strong{
      display: block;
      font-size: 1.1em;
      font-weight: 800;
    }
    small{
      font-family: 1em;
      color: #8B8B8B;
    }
  }
}


@media (min-width: @w-lg) {
	.princ{
    margin-top: 3.7vw;

    &__desc{
      margin-top: 0;
      width: 72000/1920vw;
    }
    &__title{
      margin-bottom: 1.7vw;
      font-size: 3600/1920vw;
      margin-right: 3vw;
    }
    &__txt{
      font-size: 1.25vw;
    }
    &__pics{
      position: absolute;
      top: -0.7vw;
      right: 4.4vw;
      width: 79400/1920vw;
      padding: 0;
    }
    &__diplom-1{
      width: 44400/1920vw;
      margin-bottom: 0.55vw;
      margin-left: auto;
    }
    &__diplom-2{
      width: 44400/1920vw;
      top: 11.5%;
    }
    &__label{
      justify-content: flex-start;
      margin-left: auto;
      width: max-content;
      margin-right: 1.4vw;

      &>i{
        width: 7400/1920vw;
        flex-basis: 7400/1920vw;
        margin-right: 0.5vw;
      }
      strong{
        font-size: 1.25vw;
      }
      small{
        font-size: 0.83vw;
      }
    }
  }
}
