.top{
  padding: 0 0 6vw;
  overflow: hidden;

  &__wrap{
    margin-top: 3vw;
    position: relative;
    background: #D1F4D8;
    border-radius: 6vw;
    padding: 2000/360vw;

    &&-1{
      padding-bottom: 32vw;
    }
  }
  &__wrap-2{
    position: relative;
  }
  &__wrap-2-2{
    display: flex;
    align-items: center;
  }
  &__link-back{
    display: block;
    width: 6vw;
    flex: 0 0 6vw;
    margin-right: 3vw;

    &:hover svg{
      fill: var(--color-main);
    }

    svg{
      fill: #000;
      transition: 0.3s fill;
    }
  }
  &__desc{
    position: relative;
    z-index: 1;
  }
  &__title{
  	display: block;
    font-weight: 700;
    font-size: 2800/360vw;
    line-height: 1.21;
  }
  &__title-2{
    position: relative;
    display: block;
    margin: 0;
    font-weight: 700;
    font-size: 6.5vw;
    line-height: 1.1;

    strong{
      color: var(--color-main);
    }
  }
  &__label{
    display: flex;
    align-items: center;
    width: max-content;
    margin-left: auto;
    margin-bottom:7vw;
    background: #fff;
    border-radius: 10vw;
    padding: 2.7vw 6.8vw;
    font-size: 1000/360vw;
    line-height: 1.2;

    span{
      display: block;
      font-weight: 800;
      font-size: 2200/360vw;
      color: var(--color-main);
      margin-right: 1000/360vw;
      line-height: 1;
    }
  }
  &__pic-1,
  &__pic-2{
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    .img-1{
      position: absolute;
      z-index: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      object-fit: contain;
      border-radius: 6vw;
      overflow: hidden;
    }
    .img-2{
      display: none;
    }
  }
  &__btns{
    margin-top: 7vw;
  }
  &__dots.swiper-pagination-bullets.swiper-pagination-horizontal{
    display: none;
  }
  &__slider{
    position: relative;
    margin-top: 3000/360vw;
  }
  &__slide{
    display: block;
    position: relative;
    border-radius: 2000/360vw;
    background: #EDF3F5;
    overflow: hidden;
    
    &:hover .link-diag{
      background: var(--color-2);
    }

    &-pic{
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;

      img{
        .positionFull();
        object-fit: cover;
      }
    }
    &-desc{
      position: relative;
      z-index: 1;
      padding: 5.2vw 30vw 2000/360vw 2000/360vw;
    }
    &-title{
      display: block;
      margin-bottom: 2.6vw;
      line-height: 1.15;
      height: 1.15*2em;
      font-weight: 700;
      font-size: 2200/360vw;
    }
    &-txt{
      margin-bottom: 1800/360vw;
      display: block;
      line-height: 1.5;
      font-size: 1200/360vw;
      color: #8B8B8B;
    }
  }

  &&_thanks{
    margin-bottom: 12vw;
  }

  &__prev{
    margin-top: 4vw;
    display: inline-block;
    font-size: 0.8em;
    color: #afb7bd;
    font-weight: 300;
    border-bottom: 1px solid;
    transition: 0.3s color;

    &:hover{
      color: var(--color-main);
      border-color: transparent;
    }

    svg{
      display: inline-block;
      margin-right: 0.2em;
      fill: currentColor;
      width: 13/22em;
    }
  }
}


@media (min-width: @w-lg) {
  .top{
    padding: 0;

    &__wrap{
      margin-top: .9vw;
      padding: 2.2vw 0 2vw 8200/1920vw;
      border-radius: 7000/1920vw;
      min-height: 44000/1920vw;

      &&-1{
        padding-bottom: 2vw;
      }
    }
    &__desc{
      width: 45%;
    }
    &__title{
      font-size: 5400/1920vw;
    }
    &__title-2{
      font-size: 5800/1920vw;

      &&_v1{
        max-width: 70%;
      }
      &&_v2{
        max-width: 60%;
      }
      &&_v3{
        max-width: 80%;
      }
    }
    &__btns{
      margin-top: 2.2vw;
    }
    &__pic-1{

      .img-1{
        border-radius: 7000/1920vw;
        height: 100%;
        object-fit: cover;
      }
      .img-2{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 113000/1680%;
        margin-right: -6%;

        &::after{
          content: '';
          display: block;
          position: absolute;
          z-index: 0;
          right: 0;
          bottom: 0;
          width: 21400/1130%;
          padding-bottom: 20500/1130%;
          margin-bottom: -4.7%;
          margin-right: 3.1%;
          background: url(../img/top/pic-1-3.png) center / contain no-repeat;
        }
        img{
          position: relative;
          z-index: 1;
        }
      }
    }
    &__pic-2{

      .img-1{
        border-radius: 7000/1920vw;
        height: 100%;
        object-fit: cover;
      }
      .img-2{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 122000/1680%;
        margin-right: -7.2%;

        img{
          position: relative;
          z-index: 1;
        }
      }
    }
    &__label{
      margin: 0;
      position: absolute;
      z-index: 1;
      top: 4600/1920vw;
      right: 7200/1920vw;
      width: 40800/1920vw;
      font-size: 0.83vw;
      padding: 0.8vw 1.7vw;

      span{
        margin-right: 1500/1920vw;
        font-size: 3400/1920vw;
      }
    }
    &__dots.swiper-pagination-bullets.swiper-pagination-horizontal{
      display: flex;
      gap: 1.15vw;
      width: 55400/1920vw;
      position: absolute;
      bottom: 1.56vw;
      left: 50%;
      margin-left: -55400/1920vw/2;

      .swiper-pagination-bullet{
        display: block;
        height: 4px;
        flex: 1;
        border-radius: 2px;
        background: #fff;
        opacity: 0.5;

        &.swiper-pagination-bullet-active{
          opacity: 1;
        }
      }
    }

    &__slider{
      margin-top: 2.08vw;
      padding: 0 2.66vw;

      .slider__nav{
        margin: 0;
        position: static;
        display: block;
      }
      .slider__arrow{
        position: absolute;
        z-index: 10;
        top: 50%;
        transform: translate(0%, -50%);
      }
      .slider__arrow_prev{
        left: -1vw;
      }
      .slider__arrow_next{
        right: -1vw;
      }

      .swiper-slide{
        padding: 0 .94vw;
      }
    }
    &__slide{
      border-radius: 2.08vw;

      &-desc{
        padding: 1.3vw 2.1vw 1.3vw;
      }
      &-title{
        margin-bottom: .5vw;
        font-size: 3200/1920vw;
      }
      &-txt{
        margin-bottom: 1vw;
        font-size: 0.83vw;
      }
    }
    &__prev{
      margin-top: 1vw;
      font-size: 1.15vw;
    }
    &__link-back{
      width: 4400/1920vw;
      flex-basis: 4400/1920vw;
      margin-right: 1vw;
    }


    &&_thanks{
      margin-bottom: 14200/1920vw;

      .top__wrap{
        padding-top: 3.6vw;
        min-height: 70000/1920vw;
      }
      .top__btns {
        margin-top: 3.6vw;
      }
    }
  }
}

