.logo{
  display: inline-block;
}
.phone{
  white-space: nowrap;
}


.close{
  display: block;
  width: 2000/360vw;
  height: 2000/360vw;
  padding: 0; 
  border: 0;
  background: none;
  opacity: 0.3;
  cursor: pointer;
  outline: none;

  &:is(svg, img){
    pointer-events: none;
  }
}

.link{
  display: inline-block;
  color: var(--color-main);
  font-weight: 700;
  border-bottom: 1px solid;
  line-height: 1.3;
  transition: 0.3s color;

  &:hover{
    color: #000;
  }

  .lg-min({
    font-size: 1.25vw;
    border-bottom-width: 2px;
  });
}

.box-1{
  display: flex;
  align-items: center;
  line-height: 1.44;
  font-size: 0.8em;

  .lg-min({
    max-width: 70000/1920vw;
    font-size: 0.94vw;
  });

  &>i{
    width: 12vw;
    flex: 0 0 12vw;
    margin-right: 4vw;

    .lg-min({
      width: 4200/1920vw;
      flex-basis: 4200/1920vw;
      margin-right: .43vw;
    });
  }
}

.list{
  margin-left: 1.5em;
  line-height: 1.55;
  font-size: 0.8em;

  .lg-min({
    font-size: 1.15vw;
  });

  li{
    margin-bottom: 0.9em;
    position: relative;
    
    &::before{
      content: '';
      display: block;
      position: absolute;
      left: -1.5em;
      top: 0.4em;
      width: 16/22em;
      height: 16/22em;
      background: url(../img/svg/li-item.svg) center / contain no-repeat;

      .lg-min({
        top: 0.5em;
      });
    }
  }

  strong{
    color: var(--color-main);
  }
}

.counter{
  display: flex;
  width: max-content;
  border: 1px solid #D4D4D4;
  border-radius: 2vw;
  height: 10vw;

  .lg-min({
    height: 5000/1920vw;
    border-radius: .73vw;
  });

  &__btn{
    height: 100%;
    width: 10vw;
    padding: 0;
    position: relative;
    display: block;
    border: 0;
    background: none;
    cursor: pointer;
    outline: none;
    color: #000;
    transition: 0.3s color;

    .lg-min({
      width: 2vw;
    });

    &[disabled]{
      color: rgba(0, 0, 0, 0.2);
      cursor: default;
    }

    &[data-type="minus"]::before,
    &[data-type="plus"]::before{
      content:'';
      display: block;
      position: absolute;
      height: 2px;
      width: 1200/40%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: currentColor;
    }
    &[data-type="plus"]::after{
      content:'';
      display: block;
      position: absolute;
      height: 2px;
      width: 1200/40%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(90deg);
      background: currentColor;
    }

    &:not([disabled]):hover{
      color: var(--color-main);
    }
  }
  &__inp{
    display: block;
    position: relative;
    height: 100%;
    width: 13vw;
    background: none;
    border: 1px solid #D4D4D4;
    border-top-width: 0;
    border-bottom-width: 0;
    text-align: center;
    font-family: var(--font-averta);
    font-size: 5vw;
    text-align: center;
    font-weight: 400;
    color: #000;
    padding: 0;
    outline: none;
    padding-bottom: 0.1em;

    .lg-min({
      width: 5400/1920vw;
      font-size: 1.04vw;
    });
  }
}

.scrollup{
  position: fixed;
  z-index: 100;
  bottom: 4vw;
  right: 4vw;
  width: 16vw;
  background: var(--color-main);
  color: #fff;
  border-radius: 50%;
  transition: 0.3s color, 0.3s background, .3s opacity, 0.5s transform;
  opacity: 0;
  transform: translateY(200%);

  .lg-min({
    width: 8000/1920vw;
    right: 2vw;
    bottom: 1vw;
  });

  &.show{
    opacity: 1;
    transform: translateY(0%);
  }

  &:hover{
    background: #F6F6F7;
    color: var(--color-main);

    &::after{
      border-color: var(--color-main);
    }
  }

  &::after{
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    border: 4px solid rgba(26, 201, 84, .14);
    transform: translate(-50%,-50%);
    transition: 0.3s border-color;
    pointer-events: none;

    .lg-min({
      border-width: 7px;
    });
  }

  svg{
    position: relative;
    z-index: 1;
    fill: currentColor;
  }
}

.fixmenu{
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  padding: 3vw 0;
  background: #fff;
  box-shadow: 0 2vw 3vw 0 rgba(0, 0, 0, 0.08);
  border-radius: 0 0 4vw;
  opacity: 0;
  transform: translateY(-200%);
  transition: 0.5s opacity, 0.5s transform;

  .lg-min({
    padding: 1.25vw 0;
    border-radius: 0 0 2vw 2vw;
  });

  &.active{
    opacity: 1;
    transform: translateY(0%);
  }

  &__row{
    display: flex;
    // justify-content: space-between;
    align-items: center;
  }
  &__phones{
    display: flex;

    .box-phones{
      margin-left: 4vw;

      .lg-min({
        margin-left: .73vw;
      });
    }
  }
  &__search{
    display: none;

    .lg-min({
      display: block;
      margin-left: 1vw;
      width: 45800/1920vw;
    });

    .search{
      background: #f6f6f7;
      font-size: 0.74vw;
    }
    .search__empty,
    .search__item{
      font-size: 0.74vw;
    }
  }
  &__cat{
    display: none;

    .lg-min({
      display: block;
      margin-left: 3300/1920vw;
      margin-right: 1vw;
    });
  }
  &__favorite,
  &__weight{
    display: none;

    .lg-min({
      display: block;
      margin-right: 0.73vw;
    });
  }
  &__profile{
    display: none;

    .lg-min({
      display: block;
      margin-left: 0.94vw;
    });
  }
  &__cart{
    display: none;

    .lg-min({
      display: block;
      margin-left: auto;
    });
  }
  &__burger{
    margin-left: auto;

    .lg-min({
      display: none;
    });
  }
}

.share{
  &__list{
    display: grid;
    gap: 2vw;
    grid-template-columns: repeat(5, 1fr);

    .lg-min({
      gap: .52vw;
    });

    li{
      width: 10vw;

      .lg-min({
        width: 5700/1920vw;
      });

      a{
        display: block;
        background: #EDF3F5;
        border-radius: 2vw;
        transition: 0.3s background;

        .lg-min({
          border-radius: 700/1920vw;
        });

        &:hover{
          background: #C6F2D5;
        }
      }
      span{
        display: none;

        .lg-min({
          margin-top: 0.2vw;
          display: block;
          font-size: .52vw;
          line-height: 1.45;
          white-space: nowrap;
        });
      }
    }
  }
}

.tooltip_info{
  display: inline-block;
  position: relative;
  top: 0.1em;
  width: 17/20em;
  color: #797979;
  transition: 0.3s color;
  vertical-align: baseline;

  &:hover{
    color: var(--color-main);
  }

  svg{
    fill: currentColor;
  }
}

.tooltip{

  ul{
    li{
      position: relative;
      margin-bottom: 0.5em;
      padding-left: 1em;
      font-size: 0.8em;
      line-height: 1.1;

      &:last-child{
        margin-bottom: 0;
      }

      .lg-min({
        font-size: .83vw;
      });

      &::before{
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: .4em;
        width: 9/16em;
        height: 9/16em;
        border-radius: 50%;
        background: var(--color-main);
      }
    }
  }
}


.requisits{
  margin-top: 10vw;

  .lg-min({
    margin-top: 3vw;
  });

  &__title{
    display: block;
    margin-bottom: 6vw;
    font-weight: 600;

    .lg-min({
      margin-bottom: 2vw;
      font-size: 0.83vw;
    });
  }
  &__list{
    display: flex;
    flex-wrap: wrap;
    gap: 4vw 10vw;

    .lg-min({
      gap: 1.76vw 2.6vw;
      font-size: 0.83vw;
    });

    li{
      small{
        color: rgb(156, 165, 172);
        font-size: 0.9em;
      }
    }
  }
  &__footer{
    margin-top: 10vw;
    padding-top: 10vw;
    border-top: 1px solid rgba(26, 201, 84, 0.4);

    .lg-min({
      margin-top: 2.8vw;
      padding-top: 2vw;
    });
  }
}

.add-favorite{
  display: flex;
  align-items: center;

  .lg-min({
    font-size: 0.83vw;
  });

  &>i{
    display: block;
    width: 8vw;
    flex: 0 0 8vw;
    margin-right: 2vw;

    .lg-min({
      width: 3400/1920vw;
      flex-basis: 3400/1920vw;
      margin-right: 0.5vw;
    });
  }
}
.add-to-cart{
  position: fixed;
  z-index: 100;
  top: 20vw;
  right: 4vw;
  display: flex;
  align-items: center;
  box-shadow: 0 .2vw 2vw 0 rgba(0, 0, 0, 0.07);
  color: #fff;
  border-radius: 2vw;
  padding: 1.3em 1.9em 1.2em 1.6em;
  font-size: 0.6em;
  font-weight: 700;
  opacity: 1;
  transform: translate(120%, 0%);
  transition: 0.5s opacity, 0.5s transform;

  .lg-min({
    top: 8vw;
    right: 1vw;
    border-radius: .52vw;
    font-size: 1.04vw;
  });

  &[data-status="ok"]{
    background: var(--color-main);

    &>i{
      background-image: url(../img/svg2/add-ok.svg);
    }
  }
  &[data-status="wrong"]{
    background: #FF3636;

    &>i{
      background-image: url(../img/svg2/add-wrong.svg);
    }
  }

  &.show{
    opacity: 1;
    transform: translate(0%,0%);
  }
  &.offset{
    .lg-min({
      top: 6vw;
    });
  }

  &>i{
    display: block;
    width: 8vw;
    flex: 0 0 8vw;
    aspect-ratio: 1;
    margin-right: 2vw;
    background: center / contain no-repeat;

    .lg-min({
      width: 4600/1920vw;
      flex-basis: 4600/1920vw;
      margin-right: 0.82vw;
    });
  }
  &>a{
    display: block;
    align-self: center;
    margin-left: .6em;
    color: #fff;
    transition: .3s color;
    width: 6vw;
    flex: 0 0 6vw;

    .lg-min({
      width: 4400/1920vw;
      flex-basis: 4400/1920vw;
    });

    &:hover{
      color: var(--color-2);
    }

    svg{
      fill: currentColor;
    }
  }
}

.map__icon{
  cursor: pointer;
  width: 53px;
  height: 69px;
  position: relative;
  transform: translate(-50%, -100%);
}


