.messengers{
  
  &__label{
    display: block;
    margin-bottom: 3vw;
    font-size: 0.9em;

    .lg-min({
      margin-bottom: 1vw;
      font-size: 0.94vw;
    });
  }
  &__wrap{
    display: flex;
    gap: 4vw;

    .lg-min({
      gap: .52vw;
    });
  }
  &__item{
    display: block;
    padding: 0;
    width: 12vw;
    border-radius: 50%;
    border: 2px solid #F5FBFC;
    background: #F5FBFC;
    transition-duration: .4s;
    transition-property: border-color;

    .lg-min({
      width: 6400/1920vw;
    });

    &:hover,
    &.active{
      border-color: var(--color-main);
    }
  }

}