.cart{
  position: relative;
  margin-top: 6vw;
  padding-bottom: 12vw;

  &__items{
  }
  &__item{
    margin-bottom: 6vw;
    padding: 6vw;
    border-radius: 6vw;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    background: #EDF3F5;

    &:last-child{
      margin-bottom: 0;
    }
  }
  &__pic{
    display: block;
    width: 30vw;
    margin-bottom: 4vw;
    border-radius: 2vw;
    overflow: hidden;
    position: relative;

    &::after{
      content: '';
      display: block;
      padding-bottom: 21100/215%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__btns{
    position: absolute;
    z-index: 5;
    top: 6vw;
    right: 6vw;
    display: flex;
    gap: 3vw;
  }
  &__desc{
    margin-bottom: 6vw;
    width: 100%;
  }
  &__art{
    display: block;
    margin-bottom: 2vw;
    font-size: 0.8em;
  }
  &__title{
    display: block;
    font-weight: 700;
    font-size: 1em;
    transition: 0.3s color;

    &:hover{
      color: var(--color-main);
    }
  }
  &__sizes{
    margin-top: 4vw;
    display: flex;
    flex-wrap: wrap;
    gap: 6vw;
  }
  &__price-item{
    display: block;
    margin-top: 6vw;
    font-weight: 800;
    font-size: 1.4em;
    color: var(--color-main);
    line-height: 1;
  }
  &__size{
    display: flex;
    align-items: center;
    position: relative;

    input{
      position: absolute;
      left: -9999px;

      &:checked{
        &+.cart__size-check{
          border-color: var(--color-main);
          background: var(--color-main);

          &::after{
            opacity: 1;
          }
        }
      }
      &[disabled]{
        &+.cart__size-check{
          cursor: default;
        }
        &~.cart__size-value{
          color: rgb(139, 139, 139);
          text-decoration: line-through;
          cursor: default;
        }
        
      }
    }

    &-check{
      display: block;
      width: 4vw;
      margin-right: 2vw;
      border-radius: 50%;
      border: 1px solid #CACACA;
      overflow: hidden;
      transition: 0.3s border-color, 0.3s background;
      cursor: pointer;

      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
        background: url(../img/svg/checkbox-1.svg) center / contain no-repeat;
        opacity: 0;
        transition: 0.3s opacity;
      }
    }
    &-value{
      display: block;
      cursor: pointer;
      user-select: none;
    }
  }
  &__prices{
    margin-top: 6vw;
    width: 100%;
  }
  &__label{
    display: block;
    margin-bottom: 4vw;
    font-size: 1.2em;
  }
  &__label-2{
    display: block;
    color: rgb(139, 139, 139);
    font-weight: 600;
  }
  &__price-total{
    display: block;
    font-weight: 800;
    font-size: 1.6em;
    color: var(--color-main);
  }

  &__footer{
    margin-top: 10vw;
  }
  &__bottom{
    margin-bottom: 6vw;
    font-size: 1.2em;
    font-weight: 700;
    text-align: center;
  }
  &__total{
    display: block;
    color: var(--color-main);
    font-size: 60/32em;
    font-weight: 700;
  }
  &__btns-1{
    text-align: center;
  }
  &__btns-1-1{
    margin-bottom: 6vw;
  }
  &__clear{
    display: inline-block;
    line-height: 1.3;
    color: #8B8B8B;
    font-weight: 700;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    text-underline-offset: .3em;
    transition: 0.3s color;

    &:hover{
      color: var(--color-main);
      text-decoration: none;
    }
  }
  &__count{
    display: block;
    position: absolute;
    right: 0;
    top: -14vw;
    color: rgb(139, 139, 139);
    font-size: 0.8em;
  }
  &__info{
    margin: 30vw 0;
    padding: 10vw 4vw;
    border-radius: 6vw;
    background: #D2EEF4;
    text-align: center;

    &-txt{
      margin-bottom: 8vw;
    }
  }
}


@media (min-width: @w-lg) {
	.cart{
    margin-top: 3.4vw;
    padding-bottom: 14800/1920vw;

    &__items{
    }
    &__label{
      margin-bottom: 2.3vw;
      font-size: 1.45vw;
    }
    &__item{
      margin-bottom: 1.56vw;
      padding: 3400/1920vw;
      border-radius: 3500/1920vw;
    }
    &__pic{
      margin-bottom: 0;
      margin-right: 2.08vw;
      width: 21500/1920vw;
      border-radius: 2500/1920vw;
    }
    &__btns{
      top: 1.35vw;
      right: 1.35vw;
      gap: .63vw;
    }
    &__desc{
      width: 24.4vw;
      margin: 0 2.08vw 0 0;
      align-self: flex-start;
      padding-top: .52vw;
    }
    &__art{
      margin-bottom: .3vw;
      font-size: .83vw;
    }
    &__title{
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      max-width: 100%;
      font-size: 1.25vw;
    }
    &__sizes{
      margin-top: 1.6vw;
      gap: 0.83vw;
    }
    &__size{
      font-size: 0.94vw;

      &-check{
        width: 1.35vw;
        margin-right: 0.4vw;
      }
    }
    &__price-item{
      margin-top: 1.7vw;
      font-size: 1.45vw;
    }
    &__counter{
      margin: 0 2.08vw 0 0;
      width: 16.6vw;
    }
    &__prices{
      width: 17vw;
      margin: 0;
    }
    &__label-2{
      margin-bottom: 0.2vw;
      font-size: 1.25vw;
    }
    &__price-total{
      font-size: 3600/1920vw;
    }

    &__footer{
      margin-top: 5500/1920vw;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__bottom{
      margin-bottom: 0;
      font-size: 3200/1920vw;
    }
    &__total{
      display: inline-block;
      margin-left: 0.25em;
    }
    &__btns-1{
      display: flex;
      align-items: center;
    }
    &__btns-1-1{
      margin-bottom: 0;
      margin-right: 6500/1920vw;
    }
    &__clear{
      font-size: 1.25vw;
      text-decoration-thickness: 2px;
    }
    &__count{
      top: -6.6vw;
      font-size: 1.25vw;
    }
    &__info{
      margin: 3.8vw auto 0;
      width: 72400/1920vw;
      border-radius: 2.6vw;
      padding: 2.2vw 2vw 2.7vw;
      
      &-txt{
        margin-bottom: 2vw;
        font-size: 1.45vw;
      }
    }
  }
}
