.benefits{
  margin-top: 6vw;
  position: relative;

  &__nav{
    margin-bottom: 6vw;
    display: flex;
    gap: 6vw;

    li{
      flex: 1;
    }
  }
  &__tab{
    display: block;

    &.active{
      font-weight: 700;
      color: #000;

      i{
        background: #D1F4D8;
        transition: 0.3s background;

        svg{
          fill: var(--color-main);
        }
      }
    }
    &:hover{

      i{
        background: #D1F4D8;
        transition: 0.3s background;

        svg{
          fill: var(--color-main);
        }
      }
    }

    i{
      display: block;
      position: relative;
      border-radius: 50%;
      overflow: hidden;

      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
      }

      svg{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 80%;
        fill: #000;
        transition: 0.3s fill;
      }
    }

    span{
      display: none;
    }
  }

  &__pic{
    position: relative;
    border-radius: 4vw;
    overflow: hidden;

    &::after{
      content: '';
      display: block;
      padding-bottom: 44700/629%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__desc{
    position: relative;
    background: #EDF3F5;
    border-radius: 6vw;
    padding: 6vw;
  }
  &__head{
    margin-bottom: 4vw;
    // display: flex;
    // align-items: center;
  }
  &__icon{
    display: none;
  }
  &__title{
    display: block;
    font-weight: 700;
    font-size: 1.2em;
    line-height: 1.27;
  }
  &__txt{
    line-height: 1.54;
  }
}


@media (min-width: @w-lg) {
	.benefits{
    margin-top: 3.2vw;
    display: flex;

    &__nav{
      width: 19vw;
      display: block;
      margin: 0;

      li{
        &:not(:last-child){
          margin-bottom: 1vw;
        }
      }
    }
    &__tab{
      display: flex;
      align-items: center;
      color: #8B8B8B;

      &>i{
        width: 10000/1920vw;
        flex: 0 0 10000/1920vw;
        margin-right: .83vw;
      }
      span{
        display: block;
        flex: 1;
        font-size: 0.94vw;
        line-height: 1.33;
        transition: 0.3s color;
      }
    }
    &__slider{
      width: 64.6vw;
      margin-left: auto;

      .swiper{
        overflow: visible;
      }
    }

    &__icon{
      width: 14000/1920vw;
      display: block;
      position: absolute;
      top: -5vw;
      right: 3.5vw;
      border-radius: 50%;
      overflow: hidden;
      background: #fff;
      box-shadow: .73vw .73vw 2vw rgba(56, 106, 94, 0.16);
  
      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
      }
  
      svg{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 80%;
        fill: var(--color-main);
      }
    }

    &__pic{
      position: relative;
      z-index: 1;
      width: 62900/1920vw;
      border-radius: 2.08vw;
    }
    &__desc{
      position: absolute;
      z-index: 0;
      width: 70000/1920vw;
      right: 0;
      top: -4vw;
      min-height: 47400/1920vw;
      border-radius: 3.12vw;
      padding: 3.5vw 3vw 2vw 7.6vw;
    }
    &__head{
      margin-bottom: 1.8vw;
    }
    &__title{
      font-size: 3600/1920vw;
    }
    &__txt{
      font-size: 1.15vw;
    }
  }
}
