.pagi{
  ul{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4vw;

    li{

    }
  }
  &__prev,
  &__next{
    display: block;
    padding: 0;
    background: none;
    width: 12vw;
		background: rgba(26, 201, 84, 0.20);
		border-radius: 800/360vw;
		color: var(--color-main);
		transition: 0.3s color, 0.3s background;

		&:hover{
			background: var(--color-2);
			color: #fff;
		}

		svg{
			stroke: currentColor;
		}
  }
  &__page,
  &__curr{
    display: block;
    padding: 2vw 1.2vw;
    transition: 0.3s color;
  }
  &__curr,
  a&__page:hover{
    font-weight: 700;
    color: var(--color-main);
  }

}


@media (min-width: @w-lg) {
	.pagi{

    ul{
      gap: .6vw;
    }
    &__prev,
    &__next{
      width: 5200/1920vw;
      border-radius: .52vw;
    }
    &__prev{
      margin-right: 0.7vw;
    }
    &__next{
      margin-left: 0.7vw;
    }
    &__page,
    &__curr{
      padding: .5vw;
      font-size: 1.35vw;
    }
  }
}
