.article{
  margin-top: 6vw;
  padding-bottom: 6vw;

  &__publish{
    display: block;
    font-size: 0.8em;
    line-height: 1.3;
    white-space: nowrap;
    margin-right: 10vw;
  }
  &__slogan{
    font-size: 0.8em;
    line-height: 1.3;
    text-align: right;
  }
  &__pic{
    margin-top: 4vw;
    position: relative;
    overflow: hidden;
    border-radius: 4vw;
  }
  &__head{
    display: flex;
    justify-content: space-between;
  }
  &__content{
    margin-top: 10vw;
    line-height: 1.45;

    &>*:first-child{
      margin-top: 0 !important;
    }
    &>*:last-child{
      margin-bottom: 0 !important;
    }

    h2{
      display: block;
      margin: 2.05em 0 .4em;
      font-size: 1.2em;
      font-weight: 700;
      line-height: 1.1;

      strong{
        color: var(--color-main);
      }
    }
    p{
      margin: .9em 0;
    }
    a{
      display: inline;
      text-decoration: underline;
      text-decoration-line: 1px;
      text-decoration-skip-ink: none;
      text-decoration-color: currentColor;
      color: var(--color-main);
    }
    ol{
      margin: 2.5em 0 3.5em;
      padding: 0;
      list-style: none;
      counter-reset: cntArt;
      font-weight: 600;

      li{
        margin: 0 0 1.5em;
        padding-left: 3em;
        position: relative;
        counter-increment: cntArt;

        &::before{
          content: counter(cntArt);
          display: flex;
          justify-content: center;
          align-items: center;
          width: 1.98em;
          height: 1.98em;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(0%, -50%);
          box-shadow: 0 .73vw 2vw 0 rgba(56, 106, 94, 0.16);
          background: #fff;
          border-radius: 50%;
          font-size: 40/36em;
          font-weight: 600;
          color: var(--color-main);
          line-height: 0.9;
        }
      }
    }
    ul{
      margin: 2em 0 1.8em;
      padding: 0;
      list-style: none;
      font-weight: 600;

      li{
        margin: 0 0 1.5em;
        padding-left: 1.4em;
        position: relative;

        &::before{
          content: '';
          display: block;
          width: 24/32em;
          height: 24/32em;
          position: absolute;
          left: 0;
          top: 0.4em;
          background: var(--color-main);
          border-radius: 50%;
        }
      }
    }
    img{
      margin: 3.2em 0;
      border-radius: 4vw;
    }
    blockquote{
      margin: 1.1em 0 2.7em;
      padding: 0.3em 0 0.4em 0.7em;
      border-left: 2px solid var(--color-main);
      font-weight: 600;

      p{
        margin: 0;
      }
    }
    .desktop-row{
  
      img{
        margin: 1em 0;
      }
      
    }
  }
  &__share{
    display: flex;
    align-items: center;
    gap: 2.5vw;
  }
  &__source{
    width: min-content;
    font-size: 0.8em;

    a{
      display: inline-block;
      font-weight: 600;
      color: var(--color-main);
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-decoration-thickness: 1px;
      text-underline-offset: 0.2em;

      &:hover{
        text-decoration: none;
      }
    }
  }
}


@media (min-width: @w-lg) {
	.article{
    margin-top: 3.3vw;
    padding-bottom: 3.2vw;

    &__publish{
      font-size: 1.15vw;
    }
    &__slogan{
      font-size: 1.15vw;
      width: 60000/1920vw;
    }
    &__pic{
      margin-top: 1.65vw;
      border-radius: 5600/1920vw;
    }
    
    &__content{
      margin-top: 6.2vw;
      font-size: 3200/1920vw;

      h2{
        font-size: 5800/1920vw;
      }
      img{
        border-radius: 5600/1920vw;
      }
      p, li{
        margin-right: 10vw;
      }
      blockquote{
        border-left-width: 8px;
        font-size: 2.08vw;
      }

      .desktop-row{
        display: flex;
        gap: 2.08vw;
        margin: 3.2em 0;
      
        &>div{
          flex: 1;
        }
        img{
          margin: 0;
        }
      }
    }
    &__share{
      gap: .42vw;
    }
    &__source{
      width: auto;
      margin-right: 1vw;
      font-size: 1.15vw;
    }
  }
}
