.not-found{
  padding: 20vw 0;
  text-align: center;

  &__label{
    display: block;
    line-height: 1;
    font-size: 30vw;
  }
  &__title{
    display: block;
    margin: 0 0 20vw;
    font-size: 1.2em;
  }
}


@media (min-width: @w-lg) {
	.not-found{
    padding: 3.8vw 0 5vw;

    &__label{
      font-size: 27000/1920vw;
    }
    &__title{
      margin-bottom: 3.2vw;
      font-size: 1.25vw;
    }
  }
}
