.buy{
  margin-top: 10vw;

  &__tabs{
    padding: 4vw;
    border-radius: 4vw;
    display: flex;
    flex-wrap: wrap;
    gap: 4vw 0;
    background: #F6F6F7;
    overflow: auto;

    // &.buy__tabs_v2{
    //   li{
    //     width: 50%;
    //   }
    // }

    
  }
  &.ui-tabs .ui-tabs-nav li{
    margin: 0 6vw 0 0;
    padding: 0;
  }

  .ui-tabs .ui-tabs-nav{
    
  }
  

  &__tabs-inside{
    display: flex;
    width: max-content;
    
    li{
      width: 100%;
      margin: 0;

      &:last-child{
        margin-bottom: 0;
      }
      &.ui-tabs-active a span{
        font-weight: 700;
        color: var(--color-main);
        text-decoration: none;
        letter-spacing: -.01em;
      }

      a{
        display: flex;
        align-items: center;
        padding: 0;
        font-size: 0.8em;

        &:hover{
          font-weight: 600;
          color: var(--color-main);
          letter-spacing: -.01em;

          span{
            text-decoration: none;
          }
        }

        &>i{
          display: block;
          width: 12vw;
          flex: 0 0 12vw;
          margin-right: 4vw;
          border-radius: 50%;
          background: #fff;
          box-shadow: .73vw .73vw 2vw 0px rgba(56, 106, 94, 0.16);
        }
        span{
          display: block;
          text-decoration: underline;
          text-decoration-thickness: 1px;
          text-underline-offset: .1em;
          text-decoration-skip-ink: none;
          transition: 0.3s color, 0.3s text-decoration-color;
        }
      }
    }
  }

  &__blocks{
    margin-top: 6vw;
    background: #EDF3F5;
    border-radius: 6vw;
    padding: 6vw 6vw;
  }
  &__block.ui-tabs-panel{
    padding: 0;
    position: relative;
  }
  &__head{
    display: flex;
    align-items: center;
    font-size: 1.2em;
    font-weight: 700;
    line-height: 1;

    &>i{
      display: block;
      width: 16vw;
      flex: 0 0 16vw;
      margin-right: 4vw;
      border-radius: 50%;
      background: #fff;
    }
    span{
      display: block;
    }
  }
  &__txt{
    margin-top: 4vw;
    font-size: 0.8em;
    line-height: 1.23;

    strong{
       color: var(--color-main);
    }
  }

  &__slider{
    margin-top: 8vw;
    
    &-tabs{
      display: flex;
      justify-content: space-between;
      margin-bottom: 4vw;
      position: relative;

      &[data-step="0"]::before{
        width: 0%;
      }
      &[data-step="1"]::before{
        width: 30%;
      }
      &[data-step="2"]::before{
        width: 50%;
      }
      &[data-step="3"]::before{
        width: 70%;
      }
      &[data-step="4"]::before{
        width: 100%;
      }

      &::after,
      &::before{
        content: '';
        display: block;
        position: absolute;
        z-index: 0;
        left: 0;
        top: 50%;
        width: 100%;
        margin-top: -1px;
        background: #CDDCE5;
        height: 2px;
      }
      &:before{
        width: 0%;
        background: var(--color-main);
        transition: 1s width;
        z-index: 1;
      }

      li{
        position: relative;
        z-index: 3;
        background: #EDF3F5;

        &.active{
          
          i{
            background: var(--color-main);
            border-color: #fff;
            color: #fff;
          }
        }

        span,
        small{
          display: none;
        }
        i{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 12vw;
          height: 12vw;
          border-radius: 50%;
          border: 2px solid #CDDCE5;
          font-style: normal;
          background: #EDF3F5;
          font-weight: 800;
          transition: 0.3s color, 0.3s border-color, 0.3s background;
        }
      }
      }

      
    &-scrollbar{
      margin-top: 6vw;
    }
    &-btns{
      margin-top: 12vw;
      text-align: center;
    }
  }

  &__email{
    display: inline-block;
    font-weight: 600;
    color: var(--color-main);
    line-height: 1;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    // text-decoration-thickness: 2px;
  }
  &__wrap{
    margin-top: 10vw;
    padding: 6vw;
    border-radius: 6vw;
    background: #fff;
  }
  &__label{
    display: block;
    font-weight: 600;
    margin-bottom: 3vw;
  }
  &__phones{
    line-height: 1.7;
  }
  &__col-2-1{
    margin-bottom: 6vw;

    &:last-child{
      margin-bottom: 0;
    }
  }
  &__btn-opt{
    min-width: 67vw;
  }

  &__attens{
    margin: 6vw 0;

    .buy__atten{
      margin: 0 0 4vw;
    }
  }
  &__atten{
    margin-top: 6vw;
    position: relative;
    padding-left: 1.5em;
    margin-bottom: 4vw;
    line-height: 1.4;
    font-size: 0.8em;

    &::before{
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0.3em;
      width: 18/24em;
      height: 18/24em;
      border-radius: 50%;
      background: var(--color-main);
    }
  }
  &__info{
    margin-top: 8vw;
    padding: 6vw;
    border-radius: 6vw;
    background: #E7F7EA;
    font-size: 0.8em;
    line-height: 1.8;

    &-title{
      display: block;
      margin-bottom: 3vw;
      font-size: 1.3em;
      font-weight: 700;
      line-height: 1.2;
    }

    strong{
      color: var(--color-main);
    }

    &>i{
      display: block;
      margin-bottom: 3vw;
      width: 10vw;
    }
  }

  &__reqs{
    margin-top: 4vw;
    background: #fff;
    border-radius: 4vw;
    padding: 4vw 6vw;

    &-title{
      display: block;
      margin-bottom: 3vw;
      font-size: 1.2em;
      font-weight: 700;
      color: var(--color-main);
      line-height: 1.1;
    }
    &-txt,
    &-txt-2{
      font-size: 0.8em;
      line-height: 1.58;
    }
    &-select{
      margin-bottom: 6vw;
    }
  }
  &__col-4-1{
    margin-bottom: 6vw;
  }

  .socials__link{
    padding: 0;
  }
}


@media (min-width: @w-lg) {
	.buy{
    margin-top: 3.8vw;
    padding-bottom: 3.1vw;

    &.ui-tabs .ui-tabs-nav li{
      margin: 0 1vw;
    }

    &__tabs{
      border-radius: 1.56vw;
      height: 17800/1920vw;
      padding: .7vw 3vw .8vw 3vw;
      overflow: visible;
    }

    &__tabs-inside{
      width: 100%;
      justify-content: space-between;
      align-items: center;

      // &.buy__tabs_v2{
      //   gap: 0;

      //   li{
      //     width: auto;
      //   }
      // }

      li{
        width: auto;
        margin: 0;
        // flex: 1;

        // &:nth-child(3){
        //   flex: 0 0 21.3vw;
        //   margin-left: 6vw;
        // }

        a{
          font-size: 1.25vw;

          &>i{
            width: 7000/1920vw;
            flex-basis: 7000/1920vw;
            margin-right: 1vw;
          }
        }
      }
    }

    &__blocks{
      margin-top: 3.12vw;
      border-radius: 2.08vw;
      padding: 3.7vw 4.6vw 3.5vw;
    }

    &__head{
      font-size: 3600/1920vw;

      &>i{
        width: 3.65vw;
        flex-basis: 3.65vw;
        // box-sizing: content-box;
        // border: 2px solid #fff;
        margin-right: 1.35vw;
      }
    }
    &__txt{
      margin-top: 1.2vw;
      margin-left: 5vw;
      font-size: 1.25vw;
      max-width: 72000/1920vw;
    }


    &__slider{
      margin-top: 2.8vw;
      counter-reset: cntBuy;

      .swiper-slide{
        counter-increment: cntBuy;
        padding-left: 5%;
        
        &::after{
          content: counter(cntBuy);
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          left: 0;
          top: 2.2vw;
          border: 4px solid #DCE8EF;
          background: #FFFFFF;
          width: 4.4vw;
          height: 4.4vw;
          border-radius: 50%;
          color: var(--color-main);
          font-weight: 800;
          font-size: 4800/1920vw;
        }
      }

      &-tabs{
        margin-bottom: 3vw;

        &::after,
        &::before{
          height: 6px;
          margin-top: -3px;
        }

        li{
          width: 26000/1920vw;
          position: relative;
          padding: 0 1vw .7vw;
          border: 2px solid #CDDCE5;
          border-radius: .83vw;
          transition: 0.3s color, 0.3s border-color, 0.3s background;

          &.active{
            background: var(--color-main);
            border-color: var(--color-main);
            color: #fff;
            cursor: default;
          }

          strong{
            display: flex;
            align-items: center;
            margin-top: -.7vw;
          }
          i{
            width: 6100/1920vw;
            height: 6100/1920vw;
            font-size: 3200/1920vw;
            transition: inherit;
          }
          small{
            display: block;
            position: relative;
            top: 0.1vw;
            margin-left: 0.53vw;
            font-size: 3200/1920vw;
          }
          span{
            display: block;
            margin-top: 0.5vw;
            font-weight: 600;
            font-size: 1.15vw;
          }
        }
      }
      
      &-wrap{
        max-width: 63.5vw;
        margin: 0 auto;
      }
      &-control{
        margin-top: 2.4vw;
        display: flex;
        align-items: center;
      }
      &-scrollbar{
        width: 130500/1920vw;
        margin-right: auto;
        margin-top: 0;
      }
      &-nav{
        margin-top: 0;
        width: 12200/1920vw;
      }
    }

    &__wrap{
      margin-top: 2.1vw;
      border-radius: 1.56vw;
      padding: 2.5vw 2.7vw 2.8vw;
    }
    &__row-2{
      display: flex;
      flex-wrap: wrap;
    }
    &__col-2-1{
      width: 47.6%;
      padding-right: 3%;
      // margin-right: 2%;
      margin-bottom: 2.4vw;
    }
    &__label{
      margin-bottom: 1.2vw;
      font-size: 1.25vw;
    }
    &__phones{
      columns: 2;
      font-size: 1.09vw;
      margin-right: 12vw;
      margin-top: -0.5vw;
    }
    &__btn-opt{
      min-width: 60000/1920vw;
    }

    &__attens{
      margin: 2.6vw 0 .7vw 1.7vw;
      columns: 2;

      .buy__atten{
        break-inside: avoid;
        width: auto;
        margin: 0 0 2vw;
      }
    }
    &__atten{
      margin-top: 2.6vw;
      margin-bottom: 2.1vw;
      padding-left: 0;
      margin-left: 1.7vw;
      max-width: 40vw;
      font-size: 1.25vw;

      &&_v3{
        margin-top: 2.1vw;
        max-width: 86000/1920vw;
      }

      &::before{
        left: -1.7vw;
      }
    }
    &__pic-3{
      margin-left: 1.7vw;
    }
    &__info{
      margin-top: 2.6vw;
      display: flex;
      padding: 3vw 19vw 2.8vw 2.6vw;
      font-size: 1.25vw;
      border-radius: 2.08vw;

      &-title{
        margin-top: -0.4vw;
        margin-bottom: 1vw;
        font-size: 3600/1920vw;
      }
      &>i{
        width: 6300/1920vw;
        flex: 0 0 6300/1920vw;
        margin-right: 1.6vw;
        margin-bottom: 0;
      }
    }

    &__col-3-1{
      width: 90500/1920vw;
    }
    &__col-3-2{
      width: 56700/1920vw;
      position: absolute;
      bottom: 0vw;
      right: -1.7vw;
    }
    &__reqs{
      margin-top: 0;
      border-radius: .83vw;
      padding: 1.6vw 2.2vw 2vw 2.2vw;

      &-title{
        margin-bottom: 1.9vw;
        font-size: 1.35vw;
      }
      &-txt{
        display: flex;
        font-size: 1.25vw;
        line-height: 1.72;

        &>div{
          width: 57%;
          &:nth-child(1){
            width: 43%;
          }
        }
      }
      &-txt-2{
        font-size: 1.25vw;
        line-height: 1.72;
      }
      &-select{
        margin-bottom: 2vw;
      }
    }

    &__row-4{
      display: flex;
    }
    &__col-4-1{
      width: 52300/1920vw;
      margin-right: 11400/1920vw;
      margin-bottom: 0;
    }

    .socials li{
      width: 6800/1920vw;
    }
  }
}
