.swiper{
  
  &-scrollbar-drag{
    background: var(--color-main);
  }
}
.swiper-horizontal>.swiper-scrollbar, .swiper-scrollbar.swiper-scrollbar-horizontal{
  position: relative;
  width: 100%;
  left: 0;
  height: 6px;
  border-radius: 3px;
  background: #CFF2DD;
  top: 0;

  .lg-min({
    height: .63vw;
    border-radius: .315vw;
  });
}