.sort{

  &__item-1{
    margin-bottom: 4vw;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__label{
    display: block;
    margin-right: 1em;
    font-size: .8em;
  }
  &__select-1,
  &__select-2{

    .ui-selectmenu-button.ui-button{
      padding-left: 1.3em;
      height: 14vw;
      background: #F6F6F7;
      border-width: 0 !important;
    }
    .ui-selectmenu-text{
      margin-right: 10px;
    }
  }
  &__select-1{
    width: 22vw;
  }
}


@media (min-width: @w-lg) {
	.sort{
    display: flex;
    justify-content: flex-end;

    &__item-1{
      margin-right: 3400/1920vw;
    }
    &__item-1,
    &__item-2{
      margin-bottom: 0;
    }
    &__label{
      font-size: 1.15vw;
    }

    &__select-1,
    &__select-2{

      .ui-selectmenu-button.ui-button{
        padding-left: 1.15vw;
        padding-right: 1em;
        height: 6800/1920vw;
        border-radius: .73vw;
        font-size: 1.15vw;
      }
    }
    &__select-1{
      width: 13000/1920vw;
    }
    &__select-2{
      width: 44000/1920vw;
    }
  }
}
