.advants{
  margin-top: 6vw;

  &__item{
    margin-bottom: 6vw;
    padding: 8vw;
    border-radius: 6vw;
    background: #edf3f5;
  }
  &__icon{
    display: block;
    width: 12vw;
    margin-bottom: 4vw;
  }
  &__title{
    display: block;
    margin-bottom: 3vw;
    font-size: 1.4em;
    font-weight: 700;
    line-height: 1.12;
  }
  &__txt{
    font-size: 0.8em;
    line-height: 1.69;
  }
}


@media (min-width: @w-lg) {
	.advants{
    margin-top: 2.6vw;
    display: flex;
    gap: 1.15vw;

    &__item{
      margin: 0;
      flex: 1;
      padding: 1.9vw 2vw 1.8vw 2.1vw;
      border-radius: 2.08vw;
    }
    &__icon{
      width: 6400/1920vw;
      margin-bottom: 1.4vw;
    }
    &__title{
      margin-bottom: .8vw;
      font-size: 3200/1920vw;
    }
    &__txt{
      font-size: 0.83vw;
    }
  }
}
