.section{
  position: relative;
  overflow: hidden;
  padding: 12vw 0;

  &__title{
    position: relative;
    display: block;
    margin: 0;
    font-weight: 700;
    font-size: 6.5vw;
    line-height: 64/58;
    padding-left: 6vw;

    &::before{
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-75%, -50%);
      width: 20vw;
      height: 18vw;
      background: url(../img/title.png) center / contain no-repeat;
    }

    strong{
      color: var(--color-main);
    }
  }
  &__aside{
    margin-top: 6vw;
    display: flex;
    align-items: center;
    color: #8B8B8B;
    line-height: 1.4;
    font-size: 0.8em;

    &>i{
      display: block;
      width: 12vw;
      margin-right: 4vw;
    }
  }

  &_margin-bottom{
    margin-bottom: 12vw;
  }
}


@media (min-width: @w-lg) {
	.section{
    padding: 3vw 0;

    &__title{
      padding-left: 0;
      font-size: 5800/1920vw;

      &::before{
        width: 20600/1920vw;
        height: 19000/1920vw;
        transform: translate(-114%, -49%);
      }
    }
    &__aside{
      margin: 0;
      position: absolute;
      right: 0;
      top: 1.4vw;
      text-align: right;
      font-size: 1.25vw;

      &>i{
        margin-right: 0;
        margin-left: 3200/1920vw;
        order: 2;
        width: 8000/1920vw;
      }
    }

    &_margin-bottom{
      margin-bottom: 12000/1920vw;
    }

    &&_benefits{
      padding-top: 5.6vw;

      .section__title{
        max-width: 110000/1920vw;
      }
    }
    &&_cat{
      padding-top: 3.45vw;

      .section__title{
        max-width: 45vw;
      }
    }
    &&_asort{
      padding-top: 6.9vw;

      .section__title{
        max-width: 70vw;
      }
    }
    &&_partners{
      padding-top: 4vw;

      .section__title{
        max-width: 70vw;
      }
    }
    &&_princ{
      padding-top: 4.6vw;
      padding-bottom: 5vw;

      .section__title{
        max-width: 40vw;
      }
    }
    &&_articles{
      padding-top: 4.6vw;
      padding-bottom: 2.4vw;
      overflow: visible;

      .section__title{
        max-width: 50vw;
      }
    }
    &&_info{
      padding-top: 4.6vw;
      padding-bottom: 5vw;

      .section__title{
        max-width: 50vw;
      }
    }
    &&_sales{
      padding-bottom: 8.8vw;
    }
    &&_advants{
      padding-top: 2.6vw;
      padding-bottom: 8.8vw;
    }
    &&_read{
      padding-bottom: 6.2vw;
    }
    &&_same{
      padding-bottom: 2.2vw;
    }
    &&_use{
      padding-bottom: 8.2vw;
    }

    &&_cat,
    &&_asort,
    &&_princ{
      overflow: visible;
    }
  }
}
