.about{
  margin-top: 6vw;

  &__pics{
    position: relative;
  }
  &__pic{
    position: relative;
    z-index: 3;
    overflow: hidden;
    border-radius: 4vw;

    &::after{
      content: '';
      display: block;
      padding-bottom: 48900/717%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__desc{
    margin-top: 10vw;
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    font-weight: 700;
    font-size: 1.2em;
    line-height: 1.28;

    strong{
      color: var(--color-main);
    }
  }
  &__pic-label{
    padding: 4vw 6vw;
    border-radius: 4vw;
    background: #EDF3F5;
    display: flex;
    align-items: center;
    line-height: 1.4;
    font-size: 0.8em;

    &>i{
      display: block;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 4vw;
    }
  }
}


@media (min-width: @w-lg) {
	.about{
    margin-top: 2.9vw;
    padding-bottom: 1vw;
    display: flex;

    &__pics{
      margin-left: auto;
      width: 71700/1920vw;
      order: 2;
      margin-bottom: 0;
    }
    &__pic{
      border-radius: 2.08vw;
    }
    &__desc{
      margin-top: 0;
      padding-top: .8vw;
      width: 41vw;
    }
    &__title{
      margin-bottom: 2.4vw;
      font-size: 3600/1920vw;
    }
    &__pic-label{
      position: absolute;
      z-index: 0;
      bottom: 0.5vw;
      width: 77000/1920vw;
      left: -5.8vw;
      padding: 3vw 4.4vw 2.4vw;
      font-size: 1.25vw;
      border-radius: 2.08vw;

      &>i{
        width: 6900/1920vw;
        flex-basis: 6900/1920vw;
        margin-right: 3200/1920vw;
      }
    }
  }
}
