.card{
  display: block;
  position: relative;
  height: 100%;
  overflow: hidden;
  border-radius: 6vw;
  background: #EDF3F5;

  &__pic{
    display: block;
    position: relative;
    overflow: hidden;

    &:hover img{
      transform: scale(1.05);
    }

    &::after{
      content: '';
      display: block;
      padding-bottom: 25300/382%;
    }
    img{
      .positionFull();
      z-index: 0;
      object-fit: cover;
      transition: .4s transform;
    }
  }
  &__desc{
    padding: 6vw;
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    font-weight: 700;
    line-height: 1.3;
    height: 1.3em*2;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-size: 1.2em;
    transition: 0.3s color;

    &:hover{
      color: var(--color-main);
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-decoration-thickness: 1px;
      text-underline-offset: .2em ;
    }
  }
  &__sizes{
    display: flex;
    flex-wrap: wrap;
    gap: 3vw;
    
    &-box{
      margin-bottom: 4vw;
      display: flex;
    }
    &-label{
      display: block;
      color: #8b8b8b;
      font-size: 0.8em;
      margin-right: 1em;
    }
    
  }
  &__size{
    display: flex;
    align-items: center;
    position: relative;
    font-size: 0.8em;

    &:hover{
      .card__size-check{
        border-color: var(--color-main);
      }
    }

    input{
      position: absolute;
      left: -9999px;

      &:checked{
        &+.card__size-check{
          border-color: var(--color-main);
          background: var(--color-main);

          &::after{
            opacity: 1;
          }
        }
      }
      &[disabled]{
        &+.card__size-check{
          cursor: default;
          border-color: #CACACA;
        }
        &~.card__size-value{
          color: rgb(139, 139, 139);
          text-decoration: line-through;
          cursor: default;
        }
        
      }
    }

    &-check{
      display: block;
      width: 4vw;
      margin-right: 2vw;
      border-radius: 50%;
      border: 1px solid #CACACA;
      overflow: hidden;
      transition: 0.3s border-color, 0.3s background;
      cursor: pointer;

      &::after{
        content: '';
        display: block;
        padding-bottom: 100%;
        background: url(../img/svg/checkbox-1.svg) center / contain no-repeat;
        opacity: 0;
        transition: 0.3s opacity;
      }
    }
    &-value{
      display: block;
      cursor: pointer;
      user-select: none;
    }
  }
  &__price{
    display: block;
    margin-bottom: 6vw;
    font-weight: 800;
    font-size: 1.4em;
    color: var(--color-main);
    line-height: 1;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__btn{
    min-width: 42vw;
    height: 13vw;
    font-size: 4vw;
    padding-left: .8em;
    padding-right:.8em;

    .btn1__arr{
      margin-left: 0.6em;
    }
  }

  &__btns{
    position: absolute;
    z-index: 5;
    top: 4vw;
    right: 4vw;
    display: flex;
    gap: 4vw;
  }
  &__werk{
    position: absolute;
    z-index: 3;
    bottom: 2vw;
    right: 2vw;
    max-width: calc(100% - 4vw);
    display: flex;
    align-items: center;
    padding: 1vw 3vw;
    border-radius: 2vw;
    background: #fff;
    font-size: 0.6em;

    &>i{
      display: block;
      width: 6vw;
      flex:0 0 6vw;
      margin-right: 2vw;

      img{
        position: static;
        width: 100%;
        height: auto;
      }
    }
  }
  &__no-isset{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6vw 0 4vw;
    text-align: center;
    width: 100%;
    flex: 1;
    color: #9ca5ac;
    font-weight: 700;
  }
  &__advants{
    line-height: 1.5;
    margin-bottom: 4vw;
    font-size: 0.8em;
    color: #8b8b8b;

    li{
      margin-bottom: 0.4em;
    }
  }

  &__adds{
    position: relative;

    &-1{
      position: relative;
      z-index: 1;
    }
    &-2{
      position: relative;
      z-index: 1;
      display: none;
    }
  }
  &__add-2{
    position: relative;
    z-index: 1;
    transition: 0.3s opacity;

    .btn1__arr{
      width: 1.8em;
    }
  }
  &__add-3{
    position: absolute;
    z-index: 2;
    opacity: 0;
    transition: 0.3s opacity;

    &:hover{
      opacity: 1;
    }
    &:hover+.card__add-2{
      opacity: 0;
    }
  }
}


@media (min-width: @w-lg) {
	.card{
    border-radius: 2.08vw;
    display: flex;
    flex-direction: column;
    
    &__desc{
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: .8vw 1.25vw .98vw;
    }
    &__title{
      margin-bottom: .7vw;
      font-size: 1.04vw;
    }
    &__btn{
      min-width: 18600/1920vw;
      height: 6400/1920vw;
      font-size: 1.04vw;
      box-shadow: 1.35vw 1.35vw 2.6vw 0 rgba(0, 0, 0, 0.15);
      border-radius: .83vw;

      .btn1__arr{
        margin-left: 0.6em;
      }
    }
    &__sizes{
      font-size: .73vw;
      gap: .62vw;

      &-box{
        margin-bottom: .9vw;
      }
      &-label{
        font-size: .73vw;
      }
    }
    &__size{
      font-size: .73vw;

      &-check{
        width: 1.04vw;
        margin-right: 0.5vw;
      }
    }
    &__price{
      margin-bottom: 1vw;
      font-size: 1.35vw;
    }
    &__btns{
      top: .94vw;
      right: .94vw;
      gap: .52vw;

      .square-btn{
        width: 4600/1920vw;
      }
    }
    &__werk{
      right: .94vw;
      bottom: 0.62vw;
      border-radius: .52vw;
      padding: 400/1920vw .94vw;
      font-size: 0.73vw;

      &>i{
        width: 1.15vw;
        flex-basis: 1.15vw;
        margin-right: 800/1920vw;
      }
    }
    &__no-isset{
      padding: 0 0 .5vw;
      font-size: 1.35vw;
    }
    &__advants{
      margin-top: -0.4em;
      margin-bottom: .8vw;
      font-size: 0.73vw;
    }
  }
}
