.blog{
  margin-top: 6vw;
  margin-bottom: 6vw;

  &__items{
    margin-bottom: 10vw;
  }
  &__item{
    margin-bottom: 6vw;
    padding: 6vw;
    border-radius: 6vw;
    background: rgb(237, 243, 245);
  }
  &__pic{
    display: block;
    margin-bottom: 4vw;
    position: relative;
    overflow: hidden;
    border-radius: 4vw;

    &::after{
      content: '';
      display: block;
      padding-bottom: 28200/436%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__title{
    display: block;
    margin-bottom: 4vw;
    height: 1.25*2em;
    line-height: 1.25;
    font-weight: 700;
    font-size: 1.2em;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  &__excerpt{
    display: block;
    margin-bottom: 4vw;
    height: 1.56*3em;
    line-height: 1.56;
    font-size: 0.8em;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
  &__publish{
    display: block;
    margin-bottom: 6vw;
    color: #8b8b8b;
    font-size: 0.8em;
  }
  &__filters{

    .form__box-select{
      margin-bottom: 4vw;
    }
  }
}


@media (min-width: @w-lg) {
	.blog{
    margin-top: 3.8vw;

    &__items{
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 6600/1920vw 1.56vw;
      margin-bottom: 7000/1920vw;
    }
    &__item{
      margin: 0;
      width: 54200/1920vw;
      border-radius: 2.08vw;
      padding: 2vw 2.1vw 2.3vw;
    }
    &__pic{
      margin-bottom: 1.5vw;
      border-radius: 1.56vw;
    }
    &__title{
      margin-bottom: 1.3vw;
      margin-right: -1vw;
      font-size: 3200/1920vw;
    }
    &__excerpt{
      margin-bottom: 1.3vw;
      font-size: 0.94vw;
    }
    &__publish{
      margin-bottom: 1.7vw;
      font-size: 0.83vw;
    }
    &__btn{
      min-width: 36000/1920vw;
    }
    &__filters{
      display: flex;
      margin-bottom: 2.6vw;
      gap: 3.8vw;

      .form__box-select{
        margin-bottom: 0;
      }
      .ui-selectmenu-button.ui-button{
        height: 6600/1920vw;
        font-size: 1.15vw;
      }
    }
    &__filter{
      width: 30600/1920vw;
    }
  }
}
