.ui-tabs .tabs{
  display: flex;
  gap: 4vw;

  &::before,
  &::after{
    display: none;
  }

  li{
    flex: 1;
    margin: 0;
    padding: 0;

    .ui-tabs-anchor{
      display: inline-block;
      width: 100%;
      padding: .8em 1.5em .95em;
      background: rgb(246, 246, 247);
      border-radius: 2vw;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-decoration-thickness: 1px;
      text-underline-offset: .2em;
      text-align: center;
      transition: 0.3s color, 0.3s background;

      &:hover{
        background: rgb(210, 244, 215);
        text-decoration: none;
        font-weight: 600;
        color: var(--color-main);
      }
    }

    &.ui-state-active .ui-tabs-anchor{
      box-shadow: 0vw .2vw 1vw 0vw rgba(210, 244, 215, 0.5);
      background: rgb(210, 244, 215);
      text-decoration: none;
      font-weight: 600;
      color: var(--color-main);
    }
  }
}


@media (min-width: @w-lg) {
	.ui-tabs .tabs{
    gap: .83vw;

    li{

      .ui-tabs-anchor{
        border-radius: .63vw;
        font-size: 1.09vw;
      }
    }

  }
}
