.info{
  margin-top: 6vw;
  &__slider{

    .swiper{
      overflow: visible;
    }
    .swiper-slide{
      width: 80vw;
      padding-right: 6vw;
      box-sizing: content-box;
    }
  }
  &__slide{
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 6vw;
    border-radius: 6vw;
    background: #EDF3F5;

    &:hover{

      .link-diag{
        background: var(--color-2);
      }
    }
  }
  &__pic{
    display: block;
    margin-bottom: 4vw;
    width: 12vw;
  }
  &__title{
    display: block;
    margin-bottom: 1em;
    font-weight: 700;
    line-height: 1.25;
    font-size: 1.2em;
  }
  &__txt{
    display: block;
    margin-right: 1em;
    font-size: 0.8em;
    line-height: 1.37;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .link-diag{
    flex-shrink: 0;
    width: 10vw;
  }
}


@media (min-width: @w-lg) {
	.info{
    margin-top: 4vw;

    &__slider{

      .swiper-wrapper{
        justify-content: space-between;
      }
      .swiper-slide{
        padding: 0;
        width: 40200/1920vw;
      }
    }
    &__slide{
      border-radius: 2.08vw;
      padding: 1.88vw 2.18vw 2.08vw;
    }
    &__pic{
      margin-bottom: 1.5vw;
      width: 6400/1920vw;
    }
    &__title{
      margin-bottom: .8vw;
      font-size: 3200/1920vw;
    }
    &__txt{
      font-size: .83vw;
      margin-right: 2vw;
    }

    .link-diag{
      width: 5000/1920vw;
    }
  }
}
