.tooltipster-phones{
  .tooltipster-box{
    border: 0;
    width: 16200/360vw;
    border-radius: 1600/360vw;
    background: #F0EFF3;

    .lg-min({
      border-radius: .73vw;
      width: 24400/1920vw;
    });
  }
  .tooltipster-content{
    color: #000;
    font-size: 1200/360vw;
    padding: .55em 1em;
    font-weight: 600;
    line-height: 1.1em;

    .lg-min({
      font-size: 1800/1920vw;
      padding-left: 1em;
    });
  }


  &._aside{
    .tooltipster-box{
      background: #fff;
      width: 21500/360vw;
      font-size: 1600/360vw;

      .lg-min({
        width: 35000/1920vw;
      });
    }
    .tooltipster-content{

      .lg-min({
        padding: 0.25em 1em 0.25em 24/36em;
        font-size: 3000/1920vw;
      });
    }
  }
  &._big{
    .tooltipster-box{

      .lg-min({
        width: 44800/1920vw;
      });
    }
    .tooltipster-content{

      .lg-min({
        padding: 0.25em 1em 0.25em 24/36em;
        font-size: 3600/1920vw;
      });
    }
  }
}

.tooltipster-share{
  .tooltipster-box{
    border: 0;
    border-radius: 1000/360vw;
    background: #fff;

    .lg-min({
      border-radius: .62vw;
    });
  }
  .tooltipster-content{
    color: #9CA5AC;
    padding: 2vw;

    .lg-min({
      padding: .83vw;
    });
  }
}

.tooltipster-default{
  .tooltipster-box{
    border: 0;
    border-radius: 1000/360vw;
    background: #fff;

    .lg-min({
      border-radius: .62vw;
    });
  }
  .tooltipster-content{
    color: #000;
    padding: 4vw;
    font-size: 3.5vw;
    max-width: 80vw;
    line-height: 20/16em;

    .lg-min({
      max-width: 30vw;
      font-size: 0.94vw;
      padding: 1vw 1vw 1.1vw;
    });
  }
}
.tooltipster-default-2{
  .tooltipster-box{
    border: 0;
    border-radius: 1000/360vw;
    background: #fff;

    .lg-min({
      border-radius: .62vw;
    });
  }
  .tooltipster-content{
    color: #000;
    padding: 4vw;
    font-size: 3.5vw;
    max-width: 60vw;
    line-height: 20/16em;

    .lg-min({
      max-width: 19vw;
      font-size: 0.94vw;
      padding: 1vw 2vw 1vw 1vw;
    });
    
    span[role="button"]{
      position: absolute;
      z-index: 3;
      top: 2vw;
      right: 2vw;
      width: 1em;
      cursor: pointer;
      display: block;
      align-self: center;
      margin-left: 1em;
      color: #b7b7b7;
      transition: .3s color;

      .lg-min({
        width: .52vw;
        top: .52vw;
        right: .52vw;
      });
  
      &:hover{
        color: #000;
      }
  
      svg{
        fill: currentColor;
      }
    }
  }
}
.tooltipster-default-3{
  .tooltipster-box{
    border: 0;
    border-radius: 1000/360vw;
    background: #fff;
    box-shadow: 0 0 8px 0px rgba(0, 0, 0, 0.1);

    .lg-min({
      border-radius: .62vw;
    });
  }
  .tooltipster-content{
    color: #000;
    padding: 4vw;
    font-size: 3vw;
    max-width: 80vw;
    line-height: 20/16em;

    .lg-min({
      max-width: 30vw;
      font-size: 0.73vw;
      padding: .6vw .8vw .65vw;
    });
  }
}