.footer{
  padding: 10vw 0 8vw;
	background: #1AC954;
  color: #fff;
  overflow: hidden;

  &__col{
    margin-bottom: 8vw;
  }
  &__logo{
    width: 20vw;
    margin-bottom: 6vw;
  }
  &__phones{
    margin-bottom: 6vw;
    display: flex;
    // align-items: center;

    .box-phones__label{
      padding: 0;
      background: none;
      font-size: 1800/360vw;

      &:hover::after{
        background-color: var(--color-2);background-image: url(../img/svg/dropdown-1-1a.svg);
      }

      &::after{
        display: block;
        align-self: center;
        width: 23/36em;
        flex: 0 0 23/36em;
        height: 23/36em;
        background-image: url(../img/svg/dropdown-1-1.svg);
        background-color: #fff;
        border-radius: 50%;
        margin-left: 0.4em;
        filter: drop-shadow(4px 4px 12px rgba(0, 0, 0, 0.20));
        transition: 0.3s background;
      }

      i{
        display: none;
      }
    }
  }
  &__socials{
    margin-left: 6vw;
  }
  &__label{
    display: block;
    margin-top: 1vw;
    color: rgba(255, 255, 255, 0.50);
    font-size: 0.8em;
  }
  &__email{
    display: inline-block;
    font-weight: 600;
    font-size: 1.2em;
  }

  &__menu{
    columns: 2;

    li{
      margin-bottom: 4vw;
      break-inside: avoid;

      a{
        border-bottom: 1px solid;
        transition: 0.3s border-color;
        color: #fff;
        font-weight: 600;

        &:hover{
          color: #fff;
          border-color: transparent;
          font-weight: 800;
        }
      }
    }
  }

  &__addresses{
    margin-bottom: 6vw;
    font-size: 0.9em;
    font-weight: 600;

    li{
      margin-bottom: 2vw;
    }
  }
  &__link-map{
    display: inline-flex;
    align-items: center;
    font-weight: 600;

    &:hover span{
      border-color: transparent;
      // font-weight: 800;
    }

    i{
      display: block;
      width: 1.15em;
      margin-right: 0.4em;
    }
    span{
      display: inline-block;
      line-height: 1.3;
      border-bottom: 1px solid;
      transition: 0.3s border-color;
    }
  }
  &__links{
    li{
      margin-bottom: 2vw;
    }
  }
  &__link{
    display: inline-block;
    line-height: 1.2;
    border-bottom: 1px solid;
    opacity: 0.6;
    transition: 0.3s opacity, 0.3s border-color;
    font-size: 0.8em;

    &.m{
      opacity: 1;
      font-weight: 700;
    }
    &:hover{
      opacity: 1;
      border-color: transparent;
    }
  }
}


@media (min-width: @w-lg) {
  .footer{
    padding: 5.7vw 0 1vw;

    &__row{
      display: grid;
      grid-template-areas: 'top nav'
        'links nav'
        'address nav';
      grid-template-columns: 1fr 22vw;
    }
    &__col{
      margin-bottom: 0;
    }
    &__col-1-1{
      grid-area: top;
      display: flex;
    }
    &__col-1-2{
      grid-area: nav;
      text-align: right;
    }
    &__col-1-3{
      margin-top: auto;
      grid-area: address;
      margin-bottom: 2.7vw;
      display: flex;
      align-items: baseline;
    }
    &__col-1-4{
      margin-top: 1.9vw;
      grid-area: links;
    }
    &__logo{
      width: 20200/1920vw;
      margin-bottom: 0;
      margin-right: 4600/1920vw;
    }

    &__menu{

      li{
        margin: 0 0 3.2vw;
        
        a{
          font-size: 1.56vw;
          font-weight: 600;
          border-bottom-width: 2px;
        }
      }
    }

    .box-phones{
      margin-top: -0.25vw;
    }
    .box-phones__label{
      font-size: 3600/1920vw;
    }
    &__email{
      font-size: 3600/1920vw;
    }
    &__label{
      margin-top: 0.1vw;
      font-size: 1.04vw;
    }
    &__phones{
      margin-bottom: 1.7vw;
    }

    &__socials{
      margin-left: 2vw;
    }
    .socials li{
      width: 6800/1920vw;
    }
    .socials__link{
      border-radius: .63vw;
      padding: .63vw;
      position: relative;

      &:hover::after{
        opacity: 1;
        transform: scale(84/68);
      }

      &::after{
        content: '';
        display: block;
        position: absolute;
        z-index: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        border-radius: .63vw;
        background: rgba(255, 255, 255, 0.30);
        opacity: 0;
        transition: 0.3s opacity, 0.3s transform;
        pointer-events: none;
      }
      img{
        position: relative;
        z-index: 1;
      }
    }
    
    &__addresses{
      // display: flex;
      width: 47vw;
      margin-bottom: 0;
      font-size: 1.35vw;
      
      li{
        margin-bottom:1vw;
        display: flex;
        justify-content: space-between;

        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    &__link-map{
      margin-left: 2vw;
      font-size: 1.04vw;
      white-space: nowrap;
    }
    &__links{
      display: flex;

      li{
        margin-right: 2vw;
      }
    }
    &__link{
      font-size: 1.04vw;
    }
  }
}