.partners{
  margin-top: 6vw;

  &__slider{
    
    .swiper{
      overflow: visible;
    }
    .swiper-slide{
      width: auto;
      padding-right: 4vw;
      height: auto;
    }
  }
  &__slide{
    position: relative;
    padding: 4vw;
    border-radius: 4vw;
    border: 2px solid #D9E7EB;
    // background: #FDFDFF;
    display: flex;
    flex-direction: column;
  }
  &__logo{
    height: 100px;
    margin-bottom: 4vw;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    img{
      position: relative;
      // height: 100%;
      // width: 100%;
      width: auto;
      max-width: 100%;
      max-height: 100px;
    }
  }
  &__label{
    margin-top: auto;
    display: block;
    text-align: center;
    font-weight: 600;
    font-size: 1.2em;
    white-space: nowrap;
  }
  &__scrollbar{
    margin-top: 6vw;
  }
  &__btns{
    margin-top: 12vw;
    text-align: center;
  }
  &__desc{
    margin-bottom: 8vw;
    font-size: 0.8em;
    line-height: 1.42;
  }
}


@media (min-width: @w-lg) {
	.partners{
    margin-top: 3.8vw;

    &__slider{
      .swiper-slide{
        padding-right: 3200/1920vw;
      }
    }
    &__slide{
      padding: .1vw 1.5vw 1.3vw;
      border-radius: 1.56vw;
      position: relative;
      height: 100%;
    }
    &__label{
      margin-top: 0vw;
      font-size: 1.56vw;
    }
    &__bottom{
      margin-top: 2.08vw;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__control{
      display: flex;
      align-items: center;
    }
    &__scrollbar{
      width: 86200/1920vw;
      margin-right: 3.12vw;
      margin-top: 0;
    }
    &__logo{
      margin-bottom: 1vw;
      flex: 1;
      display: flex;
      align-items: center;
      height: 11700/1920vw;

      img{
        max-height: 11700/1920vw;
      }
    }
    &__nav{
      margin-top: 0;
      width: 12200/1920vw;
    }
    &__btns{
      margin-top: 0;
    }
    &__btn{
      min-width: 25600/1920vw;
    }
    &__desc{
      margin-bottom: 3vw;
      margin-top: -0.5vw;
      font-size: 1.25vw;
      max-width: 105000/1920vw;
    }
  }
}
