.call{
  

  &&-1{
    margin-top: 6vw;
    padding: 12vw 0 0;
    background: url(../img/call/bg-1.png) center / cover no-repeat,#D2EEF4;
  }
  &&-3{
    margin-bottom: 12vw;
  }
  

  &__wrap{
    background: url(../img/call/bg-2.jpg) center / cover no-repeat,#D2EEF4;
    border-radius: 6vw;
  }
  &__desc-2,
  &__desc-3{
    padding: 6vw 6vw 0;
  }
  &__title{
    display: block;
    font-weight: 700;
    font-size: 1.4em;
  }
  &__title-2{
    position: relative;
    display: block;
    margin: 0;
    font-weight: 700;
    font-size: 6.5vw;
    line-height: 64/58;

    strong{
      color: var(--color-main);
    }
  }
  &__title-3{
    display: block;
    margin-top: 8vw;
    font-weight: 700;
    font-size: 1.2em;
    line-height: 1.25;
  }
  &__socials{
    margin-top: 4vw;
  }
  &__list{
    margin-top: 10vw;
    line-height: 1.45;
    font-size: 0.8em;

    &_v2{
      margin-top: 3vw;
    }

    li{
      margin-bottom: 4vw;
      display: flex;
      align-items: center;

      &>i{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 4vw;
        width: 14vw;
        flex: 0 0 14vw;
        height: 14vw;
        background: #fff;
        border-radius: 50%;

        img,svg{
          width: 4800/70%;
        }
      }
    }
  }
  &__info{
    margin-top: 6vw;
    display: flex;
    align-items: center;
    font-size: 0.8em;
    line-height: 1.45;

    &>i{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 12vw;
      height: 12vw;
      flex: 0 0 12vw;
      margin-right: 4vw;
      border-radius: 50%;
      background: #fff;

      img,svg{
        width: 4200/60%;
      }
    }
  }
  &__form{
    display: block;
    margin-top: 10vw;
  }
  &__row-btns{
    margin-top: 12vw;
  }



  &__pic-1{
    position: relative;
    margin: 0 -4.44vw;

    &>img{
      position: relative;
      z-index: 1;
      width: 61000/930%;
      margin-left: auto;
    }
  }
  &__pic-1_v1{
    position: relative;
    margin: 0 -4.44vw;

    &>img{
      position: relative;
      z-index: 1;
      width: 57100/789%;
      margin-left: auto;
    }
  }
  &__pic-1_v2{
    position: relative;
    margin: 6vw 4vw 0;
  }
  &__pic-1-1{
    position: absolute;
    z-index: 0;
    width: 36000/930%;
    left: 0;
    top:0;
    margin-top: 33.4%;
    margin-left: 14%;
  }
  &__pic-1-2{
    position: absolute;
    z-index: 0;
    width: 23100/930%;
    left: 0;
    top:0;
    margin-top: 45.3%;
  }
  &__pic-1-3{
    position: absolute;
    z-index: 0;
    width: 35300/789%;
    left: 0;
    top:0;
    margin-top: 16.4%;
    margin-left: 0%;
  }
  &__pic-1-4{
    position: absolute;
    z-index: 3;
    width: 23100/789%;
    left: 0;
    top:0;
    margin-top: 30.3%;
    margin-left: 10.6%;
  }

  &__pic-2,
  &__pic-3{
    margin: 6vw 4vw 0;
    position: relative;
  }
  &__box-2-1{
    position: absolute;
    left: 0;
    top: 12vw;
    font-size: 0.6em;
    color: var(--color-main);
    font-weight: 700;

    &>i{
      display: block;
      width: 12vw;
      margin-bottom: 2vw;
    }
  }
  &__box-2-2{
    display: flex;
    align-items: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 4vw;
    background: #FFF;
    border-radius: 4vw;
    padding: 4vw;
    font-size: 0.6em;
    box-shadow: .73vw .73vw 2vw rgba(56, 106, 94, 0.16);
    line-height: 1.5;

    &>i{
      display: block;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 4vw;
    }
  }
  &__box-3-1{
    position: absolute;
    z-index: 2;
    bottom: 4vw;
    left: 50%;
    width: 65vw;
    transform: translate(-50%,0%);
    padding: 10vw 4vw 4vw;
    border-radius: 4vw;
    background: #fff;
    
    &>i{
      display: block;
      position: absolute;
      top: -5vw;
      left: 4vw;
      border: 3px solid #D2EEF4;
      border-radius: 50%;
      width: 12vw;
      background: #fff;
    }
    strong{
      display: block;
      font-size: 0.8em;
    }
    small{
      font-size: 0.6em;
    }
  }
}


