          line-height: 1.1;
        }
        .call__list{
          margin-top: 0;
        }
        .call__list li{
          width: 32.275vw;
          margin-bottom: 0;
        }
        .call__desc{
          padding-bottom: 4.6vw;
        }
      }
      &&_v3{
        padding-top: 2.7vw;

        .call__desc{
