      }
      .call__socials{
        margin-left: 2.6vw;

        li{
          width: 9800/1920vw;
        }
      }
    }

    &__wrap{
      border-radius: 9000/1920vw;
    }

    &__desc{
      position: relative;
      z-index: 3;
      width: 100000/1920vw;
