    }

    &__pic-1{
      width: 93000/1920vw;
      position: absolute;
      z-index: 0;
      right: -5.8vw;
      bottom: 0;
      margin: 0;
    }
    &__pic-1_v1{
      width: 78900/1920vw;
      position: absolute;
      z-index: 0;
      right: -4.3vw;
      bottom: 0;
      margin: 0;
    }
    &__pic-1_v2{
      width: 62600/1920vw;
      position: absolute;
      z-index: 0;
      right: 2.5vw;
      bottom: 0;
      margin: 0;
    }
    &__pic-2{
      width: 86000/1920vw;
      position: absolute;
      z-index: 0;
      right: 5.5vw;
      bottom: 0;
      margin: 0;
    }
    &__pic-3{
      width: 79800/1920vw;
