      border-radius: 3400/1920vw;
      font-size: 1.04vw;

      &>i{
        width: 7600/1920vw;
        flex-basis: 7600/1920vw;
        margin-right: 1.56vw;
      }
    }
    &__box-3-1{
      width: 40000/1920vw;
      border-radius: 3600/1920vw;
      padding: 3.7vw 1.6vw 1.3vw;
      transform: none;
      left: -8.3vw;
      bottom: 12.7vw;

      strong{
        font-size: 1.35vw;
