      }
      small{
        font-size: .83vw;
      }
      &>i{
        width: 5.179167vw;
        top: -2.2vw;
        left: 1.7vw;
        border-width: 600/1920vw;
      }
    }

    &__row-btns{
      margin-top: 3.5vw;
