.socials{
  display: flex;
  gap: 4vw;

  li{
    width: 12vw;
  }

  &__link{
    display: block;
    position: relative;
    padding: 2vw;
    background: #fff;
    border-radius: 2vw;

    svg{
      overflow: visible;
    }
  }
}


@media (min-width: @w-lg) {
	.socials{
    gap: 1.04vw;

    li{
      width: 9000/1920vw;
    }

    &__link{
      border-radius: .83vw;
      padding: .83vw;
      transition: 0.3s background;

      &[data-type="phone"]{

        svg{
          fill: #FF8C22;
          transition: 0.3s fill;
        }

        &:hover{
          background: #FF8C22;

          svg{
            fill: #fff;
          }
        }
      }
      &[data-type="whatsapp"]{

        &:hover{
          background: #51CD5E;
        }
      }
      &[data-type="telegram"]{

        &:hover{
          background: #36ADE1;
        }
      }
    }
  }
}
