.garanties{
  margin-top: 6vw;

  &__item{
    margin-bottom: 10vw;
    position: relative;
    background: #EDF3F5;
    padding: 6vw 6vw 8vw;
    border-radius: 6vw;
  }
  &__pic{
    position: relative;
    border-radius: 4vw;
    overflow: hidden;
    margin-bottom: 6vw;

    &::after{
      content: '';
      display: block;
      padding-bottom: 38700/719%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__head{
    display: flex;
    align-items: center;
    margin-bottom: 6vw;

    &>i{
      display: block;
      width: 10vw;
      flex: 0 0 10vw;
      margin-right: 4vw;
    }
  }
  &__title{
    display: block;
    font-weight: 700;
    line-height: 1.1;
  }
  &__txt{
    font-size: 0.8em;
  }
  &__link{
    margin-top: 4vw;
  }
}


@media (min-width: @w-lg) {
	.garanties{
    margin-top: 3.6vw;
    margin-bottom: 8.9vw;

    &__item{
      margin-bottom: 4500/1920vw;
      padding: 2.08vw;
      border-radius: 2.08vw;
      display: flex;
    }
    &__pic{
      order: 2;
      width: 71900/1920vw;
      flex: 0 0 71900/1920vw;
      margin: 0;
      border-radius: 1.45vw;
    }
    &__desc{
      flex: 1;
      margin-right: 3vw;
      order: 1;
      padding-top: 1vw;
    }
    &__head{
      margin-bottom: 1vw;
      
      &>i{
        width: 7000/1920vw;
        flex-basis: 7000/1920vw;
        margin-right: 1.35vw;
      }
    }
    &__title{
      font-size: 3600/1920vw;
    }
    &__body{
      margin-left: 5vw;
    }
    &__txt{
      line-height: 1.95;
      font-size: 1.15vw;
    }
    &__link{
      margin-top: 2.5vw;
    }
  }
}
