.goods{
  margin-top: 6vw;

  &__wrapper{
    position: relative;
    padding: 6vw;
    border-radius: 6vw;
    background: #EDF3F5;
  }
  &__desc-mob{
    margin-bottom: 6vw;
  }
  &__header{
    display: none;
  }
  &__title{
    display: block;
    margin-bottom: 2vw;
    line-height: 1.1;
    font-weight: 700;
    font-size: 1.5em;
  }
  &__chars{
    margin-bottom: 2vw;
    line-height: 1.6;
    font-size: 0.8em;
  }
  &__ex-deliv{
    display: block;
    color: var(--color-main);
    font-weight: 700;
    font-size: 1em;

    svg{
      display: inline-block;
      width: 15/20em;
      stroke: currentColor;
    }
  }
  &__slider{
    &-main{
      position: relative;
      overflow: hidden;
      border-radius: 4vw;
    }
    &-bottom{
      margin-top: 4vw;
      display: flex;
      justify-content: space-between;
    }
    &-thumbs{
      display: none;
    }
  }

  &__body{
    margin-top: 8vw;
  }
  &__sizes{
    &-label{
      display: block;
      margin-bottom: 2vw;
      color: #8b8b8b;
    }
  }
  &__total-price{
    margin-top: 6vw;
    display: flex;
    align-items: center;
    font-size: 1.1em;

    strong{
      display: block;
      margin-left: 0.3em;
      font-size: 1.4em;
      font-weight: 800;
    }
  }
  &__links{
    margin-top: 6vw;
    display: flex;
    flex-wrap: wrap;
    gap: 2vw 4vw;
    font-size: 0.7em;

    li{
      display: flex;
      align-items: center;

      i{
        display: block;
        width: 10vw;
      }
      a{
        display: block;
        line-height: 1.4;
        border-bottom: 1px solid;
        transition: 0.3s color, 0.3s border-color;

        &:hover{
          color: var(--color-main);
          border-color: transparent;
        }
      }
    }
  }

  &__aside-btns{
    display: flex;
    gap: 4vw;
    width: max-content;
    margin: 0 0 4vw auto;
  }

  &__info{
    &.ui-tabs .buy__tabs{
      border-radius: 4vw 4vw 0 0 ;
    }
    &.ui-tabs .ui-tabs-panel{
      padding: 0;
    }
  }
  &__blocks{
    position: relative;
    border-radius: 0 0 4vw 4vw;
    border: 1px solid #F6F6F7;
  }
  &__block-1,
  &__block-2{
    padding: 6vw 6vw 6vw;
  }
  &__advants{
    display: grid;
    gap: 4vw;
    
    li{
      display: flex;
      align-items: center;

      &>i{
        display: block;
        width: 12vw;
        flex: 0 0 12vw;
        margin-right: 4vw;
      }
      span{
        display: block;
        line-height: 1.5;
        color: #9ca5ac;
      }
    }
  }
  &__txt-1{
    h2{
      display: block;
      position: relative;
      margin-bottom: 4vw;
      font-size: 1.1em;
      font-weight: 600;
      line-height: 1.1;
    }
    p{
      margin-bottom: 1.3em;
    }
    ul{
      margin-left: 1.5em;

      li{
        margin-bottom: 1.3em;
        position: relative;
        
        &::before{
          content: '';
          display: block;
          position: absolute;
          left: -1.5em;
          top: 0.4em;
          width: 16/22em;
          height: 16/22em;
          background: url(../img/svg/li-item.svg) center / contain no-repeat;
        }
      }
    }
  }
}


@media (min-width: @w-lg) {
	.goods{
    margin-top: 2.9vw;
    
    &__wrapper{
      display: flex;
      align-items: flex-start;
      padding: 3vw 3vw 2.8vw 2.8vw;
      border-radius: 2.08vw;
    }
    &__desc-mob{
      display: none;
    }
    &__header{
      display: block;
      margin-bottom: 2.5vw;
    }
    &__slider{
      width: 61000/1920vw;
      margin-right: 6400/1920vw;
    }
    &__desc{
      flex: 1;
      margin-right: 6vw;
      padding-top: 0.6vw;
    }
    &__title{
      margin-bottom: 1vw;
      font-size: 3600/1920vw;
    }
    &__chars{
      margin-bottom: 0.6vw;
      display: flex;
      flex-wrap: wrap;
      gap: 1.45vw;
      font-size: 0.83vw;
    }
    &__ex-deliv{
      font-size: 1.04vw;
    }
    &__slider{
      display: flex;

      &-main{
        order: 2;
        width: 49000/1920vw;
        margin-left: auto;
        border-radius: 2.08vw;
      }
      &-bottom{
        margin-top: 0;
        flex-direction: column;
        align-items: center;
        width: 10300/1920vw;

        .slider__arrow{
          transform: rotate(90deg);
        }
      }
      &-thumbs{
        position: relative;
        display: block;
        width: 100%;
        margin: .52vw 0;
        
        .swiper{
          height: 37000/1920vw;
        }
        .swiper-slide{
          padding: .48vw 0;
          box-sizing: content-box;
          max-height: 10300/1920vw;
          opacity: 0.6;
          transition: 0.2s opacity;
          cursor: pointer;

          &.swiper-slide-thumb-active{
            opacity: 1;
          }
        }
      }
      &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 2500/1920vw;
      }
    }

    &__body{
      margin-top: 0;

      .card__price{
        margin-bottom: 1.1vw;
        font-size: 1.45vw;
      }
      .card__bottom{
        justify-content: flex-start;
      }
      .card__adds{
        margin-left: 1.3vw;
      }
    }

    &__total-price{
      margin-top: 1.1vw;
      font-size: 1.04vw;

      strong{
        font-size: 3600/1920vw;
      }
    }
    &__links{
      margin-top: 1.4vw;
      margin-left: -0.5vw;
      gap: 1.25vw;
      font-size: .83vw;

      li{
        i{
          width: 5100/1920vw;
        }
      }
    }

    &__aside-btns{
      position: absolute;
      z-index: 10;
      top: 3.5vw;
      right: 2.7vw;
      gap: 1vw;
      flex-direction: column;
    }

    &__info{

      &.buy{
        margin-top: 3.1vw;
      }
      &.ui-tabs .buy__tabs li a{
        font-size: 3200/1920vw;
      }
      &.ui-tabs .buy__tabs{
        height: 6.5vw;
        border-radius: 1.56vw 1.56vw 0 0;
      }
    }

    &__blocks{
      border-radius: 0 0 1.56vw 1.56vw;
      border-width: 2px;
    }
    &__block-1{
      padding: 2.25vw 5vw 2.5vw 5vw;
    }
    &__block-2{
      padding: 2.85vw 5vw 1.3vw 7.1vw;
    }
    &__advants{
      grid-template-columns: repeat(4, auto);
      gap: 2vw;

      li{
        font-size: 1.25vw;

        &>i{
          width: 5000/1920vw;
          flex-basis: 5000/1920vw;
          margin-right: 1vw;
        }
        span{
          margin-bottom: 0.2vw;
        }
      }
    }
    &__txt-1{
      position: relative;
      width: 110000/1920vw;
      padding-left: 2.7vw;
      padding-top: .5vw;
      font-size: 1.04vw;
      line-height: 1.8;

      &::before{
        content: '';
        display: block;
        position: absolute;
        left: -0.3vw;
        top: 0.2vw;
        width: 4300/1920vw;
        height: 4300/1920vw;
        background: url(../img/partners/icon-2-1.svg) center / contain no-repeat;
      }

      h2{
        margin-bottom: 1.8vw;
        font-size: 1.45vw;
      }
      ul li{
        &::before{
          top: 0.6em;
        }
      }
    }
  }
}
