.cat{
  margin-top: 6vw;

  &__slider{

    .swiper{
      overflow: visible;
    }
    .swiper-slide{
      box-sizing: content-box;
      width: 80vw;
      padding-right: 5vw;
    }
  }
  &__slide{
    display: block;
    position: relative;
    padding: 6vw;
    border-radius: 6vw;
    background: #EDF3F5;

    &:hover .link-diag{
      background: var(--color-2);
    }
  }
  &__pic{
    position: relative;
    border-radius: 4vw;
    overflow: hidden;

    &::after{
      content: '';
      display: block;
      padding-bottom: 25700/324%;
    }
    img{
      .positionFull();
      object-fit: cover;
    }
  }
  &__desc{
    margin-top: 4vw;
  }
  &__title{
    display: flex;
    align-items: center;
    font-size: 1.3em;
    font-weight: 700;
    line-height: 1.25;
    height: 1.25*2em;
  }
  &__footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__count{
    color: #8B8B8B;
    font-size: 0.8em;
  }
  &__btns{
    margin-top: 12vw;
    text-align: center;
  }
}


@media (min-width: @w-lg) {
	.cat{
    margin-top: 3.9vw;

    &__slider{
      .swiper-wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 5000/1920vw 3200/1920vw;
      }
      .swiper-slide{
        padding: 0;
        width: auto;
      }
    }
    &__slide{
      border-radius: 2.08vw;
      padding: 3600/1920vw 3600/1920vw 1.6vw;
    }
    &__pic{
      border-radius: 1.56vw;
    }
    &__desc{
      margin-top: 1.2vw;
    }
    &__title{
      font-size: 3200/1920vw;
    }
    &__count{
      font-size: .83vw;
    }
    &__footer{
      margin-top: 0.1vw;
    }
    &__btns{
      margin-top: 2.7vw;
    }
  }
}
